<template lang="html">
  <!-- Loader -->
  <transition name="loader">
    <div id="wrapper" v-if="!kartlastet">
      <v-container fluid fill-height pa-0 ma-0 secondary>
        <v-layout row wrap pa-0 ma-0 justify-center align-center>
          <v-flex xs8 d-flex justify-center>
            <v-layout column justify-center align-center>
              <span class="display-1 pb-5">Laster inn kartet</span>
              <img class="image roter-bilde" src="https://norse-mythology.org/wp-content/uploads/2016/06/Vegvisir.svg_.png">
            </v-layout>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </transition>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  data() {
    return {
      attach: this.element ? this.element : false
    }
  },
  props: ['kartlastet'],
  methods: {
    fjernLoader () {
      //velg elementer
      var loader = document.querySelector('#loader')
      var body = document.querySelector('body')
      var bilde = document.querySelector('#loader img')
      //Fjern loader
      setTimeout(function () {
        loader.classList.toggle('gjemt');
        bilde.classList.remove('roter-bilde')
        bilde.classList.add('scale');
        setTimeout(function () {
          loader.classList.toggle('skjult');
          body.removeChild(loader);
        }, 1000)
      }, 2000)
    }
  }
}
</script>

<style lang="css">
#loader  {;
  background-color: #bfc8ba !important;
  transition: all 2s;
}
img{
  height: 30vh;
}
.roter-bilde{
  -webkit-animation:spin 8s ease infinite;
  -moz-animation:spin 8s ease infinite;
  animation:spin 8s ease infinite;
}
@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.loader-enter-active,.loader-leave-active {
  /* animation: fadelate 0.2s; */
  transition: all 1.5s ease;
  -webkit-transform: scale(1.5);
  transform: scale(1.5);
  animation: fade 1.5s;
}
.loader-enter{
}
.loader-leave-to
{
  /* -webkit-transform: scale(1);
  transform:  scale(1);
 -webkit-transform:  rotate(360deg);
 transform:  rotate(360deg); */
}

@keyframes fade {
  0% {
    opacity: 0.9;
  }

  60% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

/* @keyframes scaleLogo {
  from {
    -webkit-transform: scale(1);
    transform:  scale(1);
   -webkit-transform:  rotate(360deg);
   transform:  rotate(360deg);
 }
  to {
    -webkit-transform: scale(2) rotate(360deg);
    transform: scale(2) rotate(360deg);
  opacity: 0;}
} */
#wrapper{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height:100%;
  opacity: 0.9;
  background-color: rgb(169, 186, 178);
  display: flex;
  flex-flow: row wrap;
  z-index: 50;
  overflow: hidden;
}
</style>
