import { localStore } from '@/storage'

export const queryStrParse =  {
  methods: {
    parseAuthCode() {
      // const localStore = storageFactory(localStorage);
      if (window.location.search) {
        let queryString = window.location.search; // henter querystring
        queryString = queryString.substring(1); // Fjerner ? fra starten

        let sok = {} // Oppretter et objekt for å lagre sok-parametre
        let sokliste = queryString.split('&') // deler opp soksparametrene i elementer

        sokliste.forEach(function(obj){ // itererer over soks-elementer
          let soklisteObj = obj.split('=') // deler opp elementene i type og verdi
          sok[soklisteObj [0]]=soklisteObj [1] // legger enkeltparametrene i søksobjekt
        })

        if (localStore.getItem('auth_code') !== sok.code) {
          localStore.setItem('auth_code', sok.code)
        }

        return sok
      } else {
        return null
      }

      console.log(sokliste);
    }
  }
}
