<template lang="html">
    <v-card :key="item.id" :class="[{'elevation-20':kartsynlig}, 'pa-0', 'mifarge2']">
      <v-card-title primary-title class="px-4">
        <v-layout row wrap justify-space-between>
          <v-flex>
            <v-layout column wrap>
              <span class="title">{{ item.name }}</span><span>{{ item.start_date_local | moment("DD-MM-YYYY")}}</span>
            </v-layout>
          </v-flex>
          <v-flex class="text-xs-right">
            <v-btn color="transparent" icon style="position:relative; top:-24px;right:-15px" @click="leggTilSpor(item.id)">
              <v-progress-circular
                    v-if="lasterSpor"
                    color="light-green"
                    indeterminate
                  ></v-progress-circular>
                  <v-icon medium v-if="!lasterSpor">map</v-icon>
                </v-btn>
          </v-flex>
        </v-layout>

      </v-card-title>
      <v-card-text class="px-4 my-0 py-0">
        <p>Type: {{ item.type }}</p>
        <p>Distanse: {{ item.distance}} m</p>
        <p>Høydestigning: {{item.total_elevation_gain}} m</p>
        <a href="#"></a>
      </v-card-text>
      <v-card  color="transparent" flat tile class="pa-0 ma-0 text-xs-center">
        <v-btn class="mb-3" color="light-green" @click="aktiverKart"><span v-if="!kartsynlig">Oversiktskart</span><span v-if="kartsynlig">Lukk</span></v-btn>
        <transition name="fadeHeight" mode="out-in">
          <lmap v-if="kartsynlig" :linje="item.map.summary_polyline" :start="item.start_latlng" :finish="item.end_latlng"></lmap>
          <!-- <gmap v-if="kartsynlig" :linje="item.map.summary_polyline"></gmap> -->
        </transition>
      </v-card>
    </v-card>

</template>

<script>
import { mapState, mapMutations } from 'vuex'
import decode from '@/assets/js/decodePolyline'
import createGpx from 'gps-to-gpx';
import axios from 'axios'
import { localStore } from '@/storage'
import Lmap from '@/components/modals/Leafmap'
import { indexOfClosest, transformerKoord, addPtGraphic, range } from '@/assets/js/maptools'
import { kmSymbol, startSluttSymbol } from '@/assets/js/mapSettings'

export default {
  data(){
    return {
      lasterSpor: false,
      item: null,
      apiUrl:'https://kart.skoiddlos.com/turapp',
      testGpx: null
    }
  },
  props: [ 'spor' ],
  components: { Lmap },
  computed: {
    ...mapState(['kartView', 'stravaSynlig', 'aktivtOversiktskart', 'stravaSpor']),
    stravaAutorisert: () => {
      return localStore.getItem('auth_code')
    },
    kartsynlig() {
      return this.aktivtOversiktskart === this.item.id
    }
  },
  watch: {
    stravaSynlig: function(val) {
      if(!val && this.kartsynlig){
        this.$store.commit('SET_OVERSIKTSKART', null)

      }
    }
  },
  methods: {
    ...mapMutations(['SET_STRAVA', 'SET_OVERSIKTSKART']),
    leggTilSpor(id) {
      let sporInfo = this.stravaSpor.find( obj => obj.id = id)
      let attributes = {
        navn: sporInfo.name,
        type: sporInfo.type,
        lengde: sporInfo.distance,
        hoyest: sporInfo.elev_high,
        lavest: sporInfo.elev_low,
        dato: sporInfo.start_date_local
      }
      console.log(sporInfo);
      this.lasterSpor = true
      // axios.post(this.apiUrl + '/dekodelinje', {"code": this.stravaAutorisert ,"id": id})
      this.kartView.graphics.removeAll()
      axios.post(this.apiUrl + '/aktivitetstreams', {"code": this.stravaAutorisert ,"id": id})
      .then((response) => {
        // TODO: Prøv å fikse dette her
        let dist = response.data.data.avstand
        // console.log(dist);
        let avstandMaks = parseInt(String(Math.max(...dist)/1000).split('.')[0])

        this.kartView.leggTilSegment(response.data.data.koord, attributes)

        let hoyestIndex = response.data.data.hoyde.indexOf(Math.max(...response.data.data.hoyde))
        let hoyestKoord = response.data.data.koord[hoyestIndex]
        let grafikklag = this.kartView.kartlag[2]
        transformerKoord(hoyestKoord[0], hoyestKoord[1], 84, 23)
        .then(response => {
          let marker = startSluttSymbol
          marker.symbolLayers["0"].material.color = "rgba(84, 180, 255, 0.82)"
          addPtGraphic({x:response.data.ost,y: response.data.nord}, null, marker, grafikklag )
        })

        let start = response.data.data.koord[0]
        transformerKoord(start[0], start[1], 84, 23)
        .then(response => {
          let marker = startSluttSymbol
          marker.symbolLayers["0"].material.color = "rgba(8, 231, 5, 0.82)"
          addPtGraphic({x:response.data.ost,y: response.data.nord}, null, marker, grafikklag )
        })

        let slutt = response.data.data.koord[response.data.data.koord.length - 1]
        transformerKoord(slutt[0], slutt[1], 84, 23)
        .then(response => {
          let marker = startSluttSymbol
          marker.symbolLayers["0"].material.color  = "rgba(255, 0, 0, 0.82)"
          addPtGraphic({x:response.data.ost,y: response.data.nord}, null, marker, grafikklag )
        })

        range(avstandMaks, 1).forEach(el => {
          // console.log(el);
          let koordIndex = response.data.data.koord[indexOfClosest(response.data.data.avstand, el*1000)]
          // console.log(koordIndex, 'Index');
          transformerKoord(koordIndex[0], koordIndex[1], 84, 23)
          .then(response => {
            addPtGraphic({x:response.data.ost,y: response.data.nord}, null, kmSymbol, grafikklag )
          })
        })

        // console.log(grafikklag);

        this.SET_STRAVA(false)
        this.lasterSpor = false

      })
      .catch((error) => {
        console.log(error);
      })
    },
    aktiverKart() {
      if (!this.kartsynlig) {
        this.SET_OVERSIKTSKART(this.item.id)
      } else {
        this.SET_OVERSIKTSKART(null)
      }

    }
  },
  created() {
    this.item = this.spor
    // let linje = decode(this.item.map.summary_polyline).map(el => {
    //   return [el.latitude, el.longitude]
    // })
    let linje = decode(this.item.map.summary_polyline)
    // const gpx = createGpx(linje)
    // console.log(gpx);
    // this.testGpx = gpx
  }
}
</script>

<style lang="css" scoped>
.aktivt-spor {
  border: 3px solid rgb(212, 175, 85)!important;
}
.fadeHeight-enter-active{
  transition: all 0.2s;
  animation: fadelate 0.2s;
  max-height: 300px;
}
.fadeHeight-leave-active {
  transition: all 0.2s;
  max-height: 300px;
  /* animation: fadelate 0.2s; */
}
.fadeHeight-enter{
  max-height: 300px
}
.fadeHeight-leave-to
{
  opacity: 0;
  max-height: 0px;
}
@keyframes fadelate {
    0%   {opacity: 0;}
    90%  {opacity: 30;}
    100% {opacity: 100;}
}
</style>
