<template>
  <v-layout
    row
    justify-center

  >
    <v-dialog
      attach="map"
      hide-overlay
      v-model="dialog"
      scrollable
      max-width="450px">
      <v-card color="ramme farge">
        <v-card-title primary-title class="pa-0 ma-0 ">
          <v-container fluid class="pa-0 ma-0" fill-height>
            <v-layout row wrap>
              <v-flex xs6>
                <v-card class="pa-0 ma-0 tittel-el" height="50px" flat ripple :class="[aktivInstruksjoner ? 'aktiv' : 'inaktiv']">
                  <v-layout @click="aktivInstruksjoner = true"  align-center justify-center fill-height class="knapp">
                    <h2 >Instruksjoner</h2 >
                  </v-layout>
                </v-card>
              </v-flex>
              <v-flex xs6>
                <v-card class="pa-0 ma-0 tittel-el" height="50px" flat ripple :class="[!aktivInstruksjoner ? 'aktiv' : 'inaktiv']">
                  <v-layout align-center justify-center fill-height @click="aktivInstruksjoner = false" class="knapp">
                    <h2 >Om</h2>
                  </v-layout>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-title>
        <v-card-text class="my-2">
          <instructions v-if="aktivInstruksjoner"></instructions>
          <om v-else></om>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import {mapState, mapMutations} from 'vuex'
import Instructions from '@/components/Instructions'
import Om from '@/components/Om'
import { localStore } from '@/storage'

export default {
  data() {
    return {
      aktivInstruksjoner: true
    }
  },
  components: {
    Instructions, Om
  },
  computed: {
    ...mapState(['modalSynlig']),
    dialog: {
      set(value) {
        this.$store.commit('SET_DIALOG', value)
      },
      get() {
        return this.modalSynlig
      }
    }
  },
  watch: {
    dialog: function (val) {

      console.log(val, "Endring");
      if (!val) {
        console.log("Lukket");
        localStore.setItem('eksisterendebruker', "ja")
        console.log(localStore.getItem('eksisterendebruker'), 'Ny bruekrverdi');
      }
    }
  },
  methods: {
    alert() {
      alert('click')
    }
  },
  created() {
    localStore.getItem('eksisterendebruker', 'Init verdi')
  }
}
</script>

<style lang="css" scoped>
.inaktiv{
  background-color: #e1e9dc !important;
  color: rgb(94, 94, 94) !important;
  font-style: italic;
}

.aktiv{
  /* background-image: linear-gradient(#989d95, #c6cfc1); */
  background-color: #c6cfc1 !important;
  color: inherit !important;
  font-style: normal;
  /* border-bottom: none !important; */
}

.knapp{
  cursor: pointer;
}


.ramme {
  border: 2px solid rgb(166, 170, 164) !important;
  border-radius: 4px !important;
}

.farge {
  background-color: #c6cfc1 !important;
}
</style>
