<template lang="html">
  <!-- <div id="sokInfo" v-if="!lagraGrafikk"> -->
  <div id="sokInfo" >
    <!-- Stedssøk input -->
    <div id="search">
      <v-icon @click="toggleMeny" class="knapp">menu</v-icon>
      <v-icon @click="inputFocus" class="knapp">search</v-icon>
      <input ref="stedssok" type="text" :class="{synlig: inputSynlig }" placeholder="Søk på stedsnavn" v-model="stedsNavn" @click="resetAlt" @keyup="stedSok" @blur="inputMistfocus" @keyup.esc="resetSok">
      <!-- <input type="text" placeholder="Søk på stedsnavn" :value="stedsNavn" @keyup="stedSok"> -->
    </div>

    <!-- Stedssøk resultat -->
    <div id="searchResult" v-if="visResultat">
      <ul>
        <li v-for="item in resultat" :data-x="item.x" :data-y="item.y" @click="zoomTilSted">{{ item.navn }}, {{ item.type }} i {{ item.kommune }}</li>
      </ul>
    </div>
  </div>

</template>

<script>
import axios from 'axios'
import {mapState, mapMutations} from 'vuex'

export default {
  data() {
    return {
      // stedsNavn: '',
      resultat: [],
      valgtSted: false,
      // inputSynlig: false
    }
  },
  props: ['pos'],
  computed: {
    ...mapState(['verktoy', 'kartView', 'inputSynlig']),
    lagraGrafikk() {
      if (this.$store.state.grafikk) {
        return true
      } else {
        return false
      }
    },
    navState() {
      return this.$store.state.sidenavOpen
    },
    stedsNavn: {
      get() {return this.$store.state.stedsNavn},
      set(val) {this.$store.commit('SET_STEDSNAVN', val)}
    },
    visResultat() {
      return this.stedsNavn.length > 0 && !this.verktoy.stedsSok
    }
  },
  watch: {
    stedsNavn: function (val) {
      if (val) {
        // console.log(val);
        this.inputFocus()
      } else {
        this.inputMistfocus()
      }
    }
  },
  methods: {
    ...mapMutations(['TOGGLE_VERKTOY', 'TOGGLE_MENY', 'TOGGLE_TOOL', 'SET_STEDSDATA', 'SET_INPUTSYNLIG']),
    resetAlt() {
      // this.kartView.graphics.removeAll();
      this.kartView.cursor = 'default'
      this.Stedsnavn = '';
      this.TOGGLE_TOOL({tool:'stedsSok', value:false})
      this.SET_STEDSDATA(false)
    },
    inputFocus() {
      // this.inputSynlig = true;
      this.SET_INPUTSYNLIG(true)
      this.$refs.stedssok.focus();
      this.resetAlt()
      if (window.innerWidth < 495) {
        this.TOGGLE_VERKTOY(false)
      }
    },
    inputMistfocus() {
      if (!this.verktoy.stedsSok) {
        // this.inputSynlig = false;
        this.SET_INPUTSYNLIG(false)
        this.stedssok = '';
        this.TOGGLE_VERKTOY(true)
        // this.resetSok()
      }
    },
    aapneSidenav() {
      this.$store.commit('alterSidenav', !this.navState)
    },
    resetSok() {
      this.resetAlt()
      this.TOGGLE_VERKTOY({tool: 'stedsSok', value: false})
      this.stedsNavn = '';
      this.valgtSted = true;
      this.resultat = null
    },
    stedSok(evt) {
      this.TOGGLE_TOOL({tool:'stedsSok', value:false})
      if(evt.target.value.length>0 && !this.verktoy.stedSok){
        var url = "https://ws.geonorge.no/SKWS3Index/v2/ssr/sok?"
        if(evt.target.value.split(",")[1]){
          var inputNavn = evt.target.value.split(",")[0];
          var inputEkstra = evt.target.value.split(",")[1];
        } else {
          var inputNavn = evt.target.value;
          var inputEkstra = "";
        }
        var options = {
            params: {
              navn: inputNavn + "*",
              fylkeKommuneNavnListe: inputEkstra,
              eksakteForst:true,
              antPerSide:15,
              epsgKode:25833,
            },
            responseType: 'xml'
          };
          axios.get(url, options)
          .then((response) => {
            console.log(response);
            this.resultat = response.data.stedsnavn.map((el)=>{
              return {
                navn: el.stedsnavn,
                type: el.navnetype,
                kommune: el.kommunenavn,
                fylke: el.fylkesnavn,
                x: el.aust,
                y: el.nord
              };
            });
            // console.log(this.resultat);
          });
      }
    },
    zoomTilSted(evt) {
      let pos = {
        x: parseInt(evt.target.attributes["data-x"].value),
        y: parseInt(evt.target.attributes["data-y"].value)
      };
      this.kartView.zoomTil(pos)
      // TODO: Hvorfor tar det mye lengre tid å kjøre funksjoner gjennom $parent?
      // this.$parent.kartView.zoomTil(pos)
      this.$emit('valgt-sted', pos)
      this.resetSok();
    },
    toggleMeny() {
      this.TOGGLE_MENY(true)
    }
  }
}
</script>

<style lang="css" scoped>
  #search {
    display: flex;
    flex-flow: row nowrap;
    float:left;
    margin: 10px;
    position: absolute;
    top:0;
    left:0;
    height: 40px;
    border: 2px solid #a6aaa4;
    border-radius: 4px;
    background-color: #c6cfc1;
    overflow: hidden;
    padding-left: 5px;
    transition: width 0.2s;
    z-index: 10;
  }

  #search i {
    margin: auto;
    font-size: 32px;
    transition: all 0.3s;
    user-select: none;
    position: relative;
    left:0px;
    box-sizing: border-box;
    cursor: pointer;
  }

  #search i:hover {
    font-weight: bold;
  }

  #search i:active{
    color: rgb(200, 200, 200);
    animation: scale 0.8s ease-out;
  }

  #search i:focus{
    animation: scale 0.8s ease-out;
  }

  .knapp{
    cursor: pointer;
  }

  @keyframes scale {
    50% {
      -webkit-transform: scale(1.5, 1.5);
              transform: scale(1.5, 1.5);
      opacity: 0;
    }
    99% {
      -webkit-transform: scale(0.001, 0.001);
              transform: scale(0.001, 0.001);
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(0.001, 0.001);
              transform: scale(0.001, 0.001);
      opacity: 1;
    }
  }

  input[type=text] {
    position: relative;
    border: none;
    color: white;
    top:0;
    /* left:30px; */
    width: 0px;
    height: 40px;
    box-sizing: border-box;
    font-size: 1.5em;
    font-family: 'Rambla', sans-serif;
    background-color: #c6cfc1;
    background-position: 10px 9px;
    background-repeat: no-repeat;
    padding: 5px 0px 5px 0px;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
    max-width: 400px;
    outline:none;
  }


  .synlig {
      width: 367px !important;
  }

  input:active {
    box-shadow: 1px 1px 3px rgb(186, 165, 148)
  }

  #sokInfo{
    /* position: absolute;
    left: 0px; */
    z-index: 10;
    width: 400px;
  }

  #searchResult{
    position: relative;
    left: 11px;
    top: 53px;
    padding: 5px 3px 5px 3px;
    border: 2px solid #a6aaa4;
    width: 440px;
    background-color: #c6cfc1;
    box-sizing: border-box;
    max-height: 200px;
    border-radius: 5px;
    user-select: none;
    cursor: pointer;
    overflow-y: scroll;

  }

  #searchResult .closebtn{
    position: absolute;
    right: 7px;
    top: -17px;
    text-decoration: none;
    color: black;
    font-size: 3.5em;
  }

  #searchResult ul{
    list-style-type: none;
    padding: 0 ;
    margin: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
  }

  #searchResult ul li{
    text-align: left;
    font-size: 1.2em;
    padding: 5px 0 5px 5px;
    margin:0;
    transition: all 0.2s;
  }

  #searchResult ul li:hover{
    background-color: #dddddd;
  }


  #sokResultat{
    position: absolute;
    top: 45px;
    background-color: white;
    border-radius: 4px;
    max-height: 0px;
  }

  @media (max-width: 495px) {
    #search{
      top:3px;
      left:3px;
      padding:0;
      margin:0;
    }

    .synlig {
        width: calc(100vw - 76px) !important;
    }

    input[type=text] {
      margin:1px;
      transition: none;
    }

    #sokInfo{
      position: absolute;
      width: 99vw;
      z-index: 10 ;
    }



    #searchResult{
      position: relative;
      left: 0px;
      top: 41px;
      float:left;
      width: calc(100vw - 2px);
    }
  }
</style>
