<template lang="html">
  <!-- <div id="upperRight" v-if="!lagraGrafikk"> -->
  <div id="bottomRight" :class="{maalePlassering: $parent.maale}">
    <transition name="fade" mode-out="out-in">
      <button type="button" name="undo" @click="verktoyAktiv=!verktoyAktiv" :class="{aktiv: verktoyAktiv}" v-if="!verktoyAktiv"><v-icon>build</v-icon></button>
      <button type="button" name="undo" @click="verktoyAktiv=!verktoyAktiv" :class="{faded: verktoyAktiv}" v-if="verktoyAktiv"><v-icon>close</v-icon></button>
    </transition>
    <transition name="fade" mode-out="out-in">
      <div id="toolbox" v-if="verktoyAktiv">
          <div class="knapperad right">
            <button slot="activator" type="button" @click="$parent.fullskjermVM.toggle()"><v-icon>fullscreen</v-icon></button>
            <button type="button" name="undo" @click="taBilde"><v-icon>camera_alt</v-icon></button>
            <button type="button" @click="simulerSol" :class="{'aktiv-knapp': verktoy.solsimulering}"><font-awesome-icon icon="sun" size="2x" :color="verktoy.solsimulering ? 'white' : '#616160'"/></button>
          </div>

        <div class="knapperad down row1">
          <div id="zoom">
            <button type="button" @click="maale" :class="{'aktiv-knapp': verktoy.maaleAktiv}"><v-icon :color="verktoy.maaleAktiv ? 'white' : ''">straighten</v-icon></button>
            <button type="button" @click="finnSted" :class="{'aktiv-knapp': verktoy.stedsSok}"><v-icon :color="verktoy.stedsSok ? 'white' : ''">info</v-icon></button>
            <button type="button" @click="hentSkred">
              <v-avatar
                size="30px">
                <img
                  :src= "require('@/assets/avalanche.svg')"
                  alt="John"
                >
              </v-avatar>
              <!-- <v-icon>edit</v-icon> -->
            </button>

            <!-- <v-card>
              {{map.heading}}
            </v-card> -->
          </div>
        </div>

        <div class="knapperad down row2">
          <div id="zoom">
            <button type="button" @click="tegn" :class="{'aktiv-knapp': verktoy.tegneMeny}"><v-icon>edit</v-icon>
            </button>
            <button type="button" @click="tegnforklaring" :class="{'aktiv-knapp': verktoy.tegnForklaring}"><v-icon>toc</v-icon>
            </button>
            <!-- <draw-edit></draw-edit> -->
            <!-- <button type="button" @click="tegn" :class="{'aktiv-knapp': verktoy.solsimulering}"><font-awesome-icon icon="sun" size="2x" :color="verktoy.solsimulering ? 'white' : '#616160'"/></button> -->
          </div>
        </div>
      </div>
    </transition>
    <v-snackbar
      v-model="snackbar"
      bottom
      multi-line
      :timeout="20000"
      color="rgba(124, 175, 128, 0.44)"
    >
    <v-layout row wrap justify-center align-center >
      <v-btn hover color="rgb(117, 142, 113)" class="pa-3 ma-0"><a class="" target="#" :href="bildeData" download="map_screenshot.png"@click="snackbar = false">Last ned bildet</a></v-btn>

    </v-layout>
    </v-snackbar>
  </div>

</template>

<script>
import axios from 'axios'
import {mapState, mapMutations, mapGetters, mapActions} from 'vuex'
import DrawEdit from './DrawEdit'
// import {verktoyMixin} from '../../verktoyMixin'

export default {
  // mixins: [verktoyMixin],
  props: ['view', 'lukkMeny'],
  data() {
    return {
      verktoyAktiv: false,
      zoomAktiv: false,
      bildeData: null,
      snackbar: false,
      animated: false
    }
  },
  components: { DrawEdit },
  computed: {
    ...mapState(['verktoySynlig', 'innstillingerSynlig', 'modalSynlig', 'kartView', 'verktoy', 'map', 'inputSynlig', 'aktivKlikkEvent']),
    menyAapen () {

      if (this.lukkMeny) {
        this.verktoyAktiv = false;
      }
    },
    lagraGrafikk() {
      if (this.$store.state.grafikk) {
        return true
      } else {
        return false
      }
    },
    modalAktiv: {
      get () {return this.modalSynlig},
      set(val) {this.$store.commit('SET_DIALOG', val)}
    },
    innstillingerAktiv: {
      get () {return this.innstillingerSynlig},
      set(val) {this.$store.commit('SET_INNSTILLINGER', val)}
    },
    uiActive: {
      get() {return this.$store.state.uiActive},
      set(val) {this.$store.commit('oppdaterUiActive', val)}
    },
    himmelretning() {     //can be 0 - 360
        var azimuthHumans = [
                  'Nord','Nord-Øst',
                  'Øst','Sør-Øst',
                  'Sør','Sør-Vest',
                  'Vest','Nord-Vest','Nord'];

        return azimuthHumans[ Math.round(this.map.heading/45) ];
    }
  },
  methods: {
    ...mapMutations(['SET_DIALOG', 'SET_INNSTILLINGER', 'TOGGLE_TOOL', 'SET_STEDSNAVN',
                     'SET_STEDSDATA', 'SET_INPUTSYNLIG', 'SET_KLIKKEVENT', 'SET_SOLPOSISJON']),
    ...mapActions(['resetTools']),
    zoom(verdi) {
      if (verdi === 'inn') {
        this.$emit('inn')
      } else {
        this.$emit('ut')
      }
    },
    fullskjerm() {
      this.$emit('fullskjerm')
    },
    locate() {
      this.$emit('locate')
    },
    finnMeg() {
      this.getPreciseLocation()
      .then((response) => {
        this.transformerKoord(response[1], response[0], 84, 23)
        .then((response) =>{
          let pos = {x: response.data.ost, y: response.data.nord}
          this.$emit('minPlassering', pos)
        })
      })
    },
    maale() {
      this.TOGGLE_TOOL({'maaleAktiv': !this.verktoy.maaleAktiv});
    },
    tegn() {
      if (this.verktoy.tegneMeny) {
        this.resetTools()
      } else {
        this.resetTools()
        this.TOGGLE_TOOL({'tegneMeny': true});
      }

    },
    tegnforklaring() {
      if (this.verktoy.tegnForklaring) {
        this.resetTools()
      } else {
        this.resetTools()
        this.TOGGLE_TOOL({'tegnForklaring': true});
      }
    },
    finnSted() {
      if (this.verktoy.stedsSok) {
        this.resetTools()
        this.kartView.graphics.removeAll();
        this.kartView.cursor = 'default'
        this.SET_KLIKKEVENT(null)
        this.SET_INPUTSYNLIG(false)
        this.SET_STEDSDATA(false)
        this.SET_STEDSNAVN('')
        // this.aktivKlikkEvent.remove()
      } else {
        this.resetTools()
        this.TOGGLE_TOOL({'stedsSok': true})
        this.kartView.cursor = 'crosshair'
        this.kartView.stedSok()
      }

      if (this.verktoy.stedsSok) {

      } else {

      }
    },
    test() {
     alert(this.uiActive.get())
    },

    faens() {
     this.kartView.maaleLinje()
    },

    resetNord() {
     this.kartView.goTo({heading:0})
    },

    zoomTo(event) {
     this.zoomAktiv = true;
     const pos = this.kartView.toMap(event.clientX, event.clientY)
    },

    taBilde() {
    this.resetTools()
     const vm = this
     this.kartView.takeScreenshot({})
     .then(function(screenshot) {
       vm.bildeData = screenshot.dataUrl;
       vm.snackbar = true
      });
    },

    simulerSol() {
      let toolStatus = {
        punktZoom: false,
        maaleAktiv: false,
        velgExtent: false,
        stedsSok: false,
        solsimulering: false,
      }
      console.log(this.verktoy.solsimulering);
      if (!this.verktoy.solsimulering) {
        this.resetTools()
        toolStatus.solsimulering = true
        this.TOGGLE_TOOL(toolStatus)
      } else {
        this.resetTools()
        toolStatus.solsimulering = false
        this.kartView.graphics.removeAll()
      }

    },

    animate() {
      this.animated = true
    },
    hentSkred() {
      this.resetTools()
      this.kartView.hentSkreddata()
    }

 },
 mounted() {
 }
}
</script>

<style lang="css" scoped>
#bottomRight{
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: 1;
  box-sizing: border-box;
}

.knapperad{
  margin: 2px;
  position: relative;
}

.right {
  position: absolute;
  top: -2px;
  right: 42px;
  display: flex;
  flex-flow: row nowrap;
  /* float: right; */
  align-items: flex-end;
  justify-content: flex-start;
  flex-direction: row-reverse;
}

.down {
  position: absolute;
  bottom: 40px;
  display: flex;
  flex-flow: column nowrap;
}

.row1{
    right: -2px;
}

.row2 {
  right: 43px;
}

#zoom button{
  margin-bottom: 2px;
}

a{
  text-decoration: none;
  color: black
}

font-awesome-icon {
  transition: all .3s;

}

button{
  color: rgb(133, 133, 133);
  margin: 0 2px;
  height: 40px;
  width: 40px;
  border: 2px solid #a6aaa4;
  border-radius: 4px;
  background-color: #c5cfc0;
  transition: background-color 0.1s ease-in;
  cursor: pointer;
}

button:hover , .aktiv{
  background-color: #d4d5d3
}

button:active {
  background-color: #ffffff
}

button:focus{
  outline: 0
}

.right{
  justify-content: flex-end;
}

.fade-enter-active, .fade-leave-active {
transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.faded {
  opacity: 0.7!important;
}

.disabled {
  opacity: .5;
  cursor: not-allowed;
}

.aktiv-knapp {
  background-color: #a4aa9f;
  border-color: #575955 !important
}

.ingenanimasjon {
  transition: none !important;
}

.flashit{
  color:#f2f;
	-webkit-animation: flash linear 1s;
	animation: flash linear 1s;
}

@-webkit-keyframes flash {
	0% { opacity: 1; }
	50% { opacity: .1; }
	100% { opacity: 1; }
}
@keyframes flash {
	0% { opacity: 1; }
	50% { opacity: .1; }
	100% { opacity: 1; }
}

@media (max-width: 495px) {

  .maalePlassering {
    bottom: 110px !important;
  }
}
</style>
