<template>
  <v-layout
    row
    justify-center
    >
    <v-dialog
      attach="map"
      hide-overlay
      v-model="dataModal"
      scrollable
      max-width="450px"
      >
      <v-card color="ramme farge">
        <v-card-title primary-title class="pa-0 ma-0 ">
          <v-container fluid class="pa-0 ma-0" fill-height>
            <v-layout column>
              <span class="headline py-2">Legg til data</span>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs4 v-for="item in faneItems">
                  <v-card tile slot="activator" class="pa-0 ma-0 tittel-el" height="50px" flat ripple :class="[aktivFane === item.navn ? 'aktiv' : 'inaktiv']">
                    <v-layout @click="aktivFane = item.navn"  align-center justify-center fill-height class="knapp">
                        <!-- <v-icon large>{{ item.icon}}</v-icon> -->
                      <span hidden-xs-and-up>{{ item.navn }}</span >
                    </v-layout>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-layout>
          </v-container>
        </v-card-title>
        <v-card-text class="my-2">
          <gpx v-if="aktivFane === 'GPX'"></gpx>
          <polylinje v-if="aktivFane === 'Linje'"></polylinje>
          <nvdb v-if="aktivFane === 'NVDB'"></nvdb>
        </v-card-text>
      </v-card>

    </v-dialog>
  </v-layout>
</template>

<script>
import {mapState, mapGetters, mapMutations} from 'vuex'
import Gpx from './AddGpx'
import Polylinje from './AddPolyline'
import Nvdb from './AddSvv'


export default {
  data() {
    return {
      kartsynlig: false,
      aktivFane: this.fane,
      faneItems: [
        {icon: 'settings',navn:'GPX'},
        {icon: 'directions_run',navn:'NVDB'},
        {icon: 'info',navn:'Linje'},
      ]
    }
  },
  props: {
    fane: {
      default: 'GPX',
      type: String
    }
  },
  components: { Gpx, Polylinje, Nvdb},
  computed: {
    ...mapState(['addataSynlig']),
    dataModal: {
      set(value) {
        this.$store.commit('SET_ADDATA', value)
      },
      get() {
        return this.addataSynlig
      }
    },
  },
  methods: {
    ...mapMutations(['SET_ADDATA']),
    // source: http://doublespringlabs.blogspot.com.br/2012/11/decoding-polylines-from-google-maps.html

  }
}
</script>

<style lang="css" scoped>
.tittel-el{
  transition: all .3s
}

.tittel-el:hover{
  background-color: #a8b0a2 !important;
}

.inaktiv{
  background-color: #e1e9dc !important;
  color: rgb(94, 94, 94) !important;
  font-style: italic;
}

.aktiv{
  background-color: #c6cfc1 !important;
  color: inherit !important;
  font-style: normal;
  /* border-bottom: none !important; */
}

.knapp{
  cursor: pointer;
}


.ramme {
  border: 2px solid rgb(166, 170, 164) !important;
  border-radius: 4px !important;
}

.farge {
  background-color: #c6cfc1 !important;
}
</style>
