var stravaPoint = [
  {
    name:'ObjectID',
    alias: 'ObjectID',
    type: "oid"
  },
  {
    name:'type',
    alias:'Type',
    type: "string"
  },
  {
    name:'tekst',
    alias:'Tekst',
    type: "string"
  }
];

var stravaPtRenderer = {
  type: "unique-value",  // autocasts as new UniqueValueRenderer()
  field: "type",
  defaultSymbol: { type: "simple-fill" },  // autocasts as new SimpleFillSymbol()
  uniqueValueInfos: [{
    // All features with value of "North" will be blue
    value: "km",
    symbol: {
      type: "simple-marker",
      style: "diamond",
      outline: {
          color: [255, 170, 0, 1]
      },
      color: [255, 170, 0, 0.25]
    }
  }, {
    // All features with value of "East" will be green
    value: "hoyest",
    symbol: {
    type: "simple-marker",
      style: "square",
      outline: {
          color: [0, 92, 230, 1]
      },
      color: [0, 169, 230, 0.25]
    }
  }, {
    // All features with value of "South" will be red
    value: "lavest",
    symbol: {
      type: "simple-marker",  // autocasts as new SimpleFillSymbol()
      color: "red"
    }
  }, {
    // All features with value of "West" will be yellow
    value: "raskest",
    symbol: {
      type: "simple-marker",  // autocasts as new SimpleFillSymbol()
      color: "yellow"
    }
  }]
}

export { stravaPoint, stravaPtRenderer }
