<template lang="html">
  <!-- <div id="knappecontainer" v-if="!lagraGrafikk"> -->
  <div id="knappecontainer" class="noselect">
    <transition name="fade" mode="out-in">
      <div class="knapperad right" v-if="redigeringAktiv">
        <button type="button" name="undo" @click="undo"><i class="material-icons">undo</i></button>
        <button id="freehandButton" name="freehand" type="button" title="Freehand" @click="freeHand" :class="{aktiv: aktivFreehand}" v-if="['polyline', 'polygon'].includes(aktivRedKnapp)"><i class="material-icons">gesture</i></button>
      </div>
    </transition>
    <div class="knapperad">
      <button type="button" name="button" @click="resetTegning" v-if="aktivRedKnapp"><i class="material-icons" >delete_forever</i></button>
      <button type="button" name="polygon" @click="tegnObjekt('polyline')" :class="{aktiv: aktivRedKnapp === 'polyline'}" v-if="tools.includes('polyline')"><i class="material-icons" >timeline</i></button>
      <button type="button" name="polygon" @click="tegnObjekt('polygon')" :class="{aktiv: aktivRedKnapp === 'polygon'}" v-if="tools.includes('polygon')"><i class="material-icons">check_box_outline_blank</i></button>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  props: ["sketch", "view", "drawend"],
  watch: {
    drawend: function() {
      if (this.drawend) {
        this.resetKnapper();
      }

    }
  },
  data() {
    return {
      tools: ['point', 'polyline', 'polygon'],
      redigeringAktiv: false,
      aktivRedKnapp: null,
      aktivFreehand: false
    }
  },
  computed: {
    ...mapState(['kartView', 'verktoy', 'aktivDraw']),
    lagretGrafikk() {
      if (this.$store.state.grafikk) {
        return true
      } else {
        return false
      }
    }
  },
  watch: {
    lagretGrafikk: {
      handler(val) {
        if (val) {
          this.resetKnapper();
        }
      }
    }
  },
  methods: {
    ...mapGetters(['SET_AKTIVDRAW']),
    tegn() {
      this.kartView.tegneAction()
    },
    aktiverKnapp(verdi) {
      let vm = this
      this.redigeringAktiv = !this.redigeringAktiv
      this.tools = [verdi]
      if (this.aktivRedKnapp === verdi) {
        this.aktivRedKnapp = null
        this.resetKnapper()
      } else {
        this.aktivRedKnapp = verdi
        this.tools = [verdi];
        console.log(this.tools, 'textSmCenter');
      }
    },
    freeHand() {
      this.aktivFreehand = !this.aktivFreehand;
      this.aktivDraw.mode = this.aktivDraw.mode === "click" ? "hybrid" : "click";
    },
    undo() {
      this.aktivDraw.undo();
    },
    erAktiv(verdi) {
      return this.aktivRedKnapp == verdi
    },
    resetKnapper() {
      this.aktivRedKnapp = null;
      this.aktivFreehand = false;
      this.redigeringAktiv = false;
      this.kartView.graphics.removeAll()
      this.tools = ['point', 'polyline', 'polygon'];
      this.aktivDraw.destroy()
    },
    resetTegning() {
      this.resetKnapper();
      // this.view.graphics.removeAll();
    },
    tegnObjekt(verdi) {
      console.log(verdi, 'Switch');
      this.aktiverKnapp(verdi)
      console.log(this.aktivRedKnapp);
      switch (verdi) {
        case 'polyline':
          this.kartView.tegnLinje()
          break;
        case 'polygon':
          this.kartView.tegnPolygon()
          break;
        default:

      }
       // Fjerner eksisterende grafikk
       this.kartView.cursor = 'crosshair'
      this.kartView.graphics.removeAll();
      // this.$store.commit('oppdaterGrafikk', null)
      this.aktiverKnapp(verdi);
      // this.sketch.create(verdi, {mode: "click"});
      // console.log(this.sketch, 'Sketch');
    }
  }
}
</script>

<style lang="css" scoped>
  #knappecontainer{
    display: flex;
    flex-flow: column nowrap;
    z-index: 4
  }

  .knapperad{
    /* margin: 2px; */
    display: flex;
  }

  button{
    color: rgb(133, 133, 133);
    margin: 2px 2px;
    height: 40px;
    width: 40px;
    border: 2px solid #a6aaa4;
    border-radius: 4px;
    background-color: #c5cfc0;
    transition: background-color 0.1s ease-in;
    cursor: pointer;
  }

  button:hover , .aktiv{
    background-color: #d4d5d3
  }

  button:active {
    background-color: #ffffff
  }

  button:focus{
    outline: 0
  }

  .right{
    justify-content: flex-end;
  }

  .fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  /* #freehandButton {
    position: absolute;
    bottom: 35px;
    color: black;
  }

  #resetBtn {
    position: absolute;
    bottom: 70px;
    color: black;
  } */

  @media (max-width: 480px) {
    #knappecontainer{
      bottom: 0px;
      right: 0px;
    }
  }
</style>
