<template lang="html">
  <v-layout column wrap class="text-xs-left">
    <span class="headline pb-3 text-xs-center">Spor</span>
    <v-layout row wrap v-if="stravaAutorisert" justify-center>
      <v-flex xs8 justify-center v-if="!refreshData">
        <v-card tile flat class="mb-4 mifarge">
          <v-card-text>
            Finner du ikke sporet ditt? Prøv å oppdatere dataene
          </v-card-text>
        </v-card>
        <v-card color="#E1E9DC" flat tile>
          <v-card-title primary-title>
            Velg type
          </v-card-title>
          <v-card-text>
            <v-select
              :items="valgListe"
              v-model="valgtType"
              label="Velg type "
              @change="kafaens"
            ></v-select>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12 class="text-xs-center">
        <v-btn class="mifarge" @click="oppdaterData" icon v-if="!refreshData"><v-icon color="grey darken-2">refresh</v-icon></v-btn>
      </v-flex>
      <v-progress-circular
        v-if="refreshData"
        :size="70"
        :width="7"
        color="green"
        indeterminate
      ></v-progress-circular>
    </v-layout>
    <!-- Stravaspor -->
    <v-container grid-list-lg v-if="!refreshData">
      <v-layout column wrap>
        <v-flex v-for="item in filtrertListe">
          <spor :spor="item"></spor>
        </v-flex>
      </v-layout>
    </v-container>
    <v-layout row wrap v-else>
      <v-flex v-if="!stravaAutorisert">
        <p>Mangler autorisasjon</p>
        <p>Gi applikasonen tilgang til din data her <v-btn color="green lighten-4" :href="'https://www.strava.com/oauth/authorize?client_id=26643&response_type=code&redirect_uri=' + aktivUrl + '&approval_prompt=force&scope=view_private'" v-if="!stravaAutorisert">Autoriser</v-btn></p>
      </v-flex>
    </v-layout>
    <v-flex xs12 class="text-xs-center pa-4" v-if="!refreshData">
      <v-progress-circular
        v-if="lasterMerData"
        :size="70"
        :width="7"
        color="green"
        indeterminate
        class="pa-4"
      ></v-progress-circular>
      <div class="" v-if="!lasterMerData">
        <p>Hent fler data</p>
        <v-btn class="mifarge" @click="hentMerData" icon><v-icon color="grey darken-2">refresh</v-icon></v-btn>
      </div>
    </v-flex>
  </v-layout>

</template>

<script>
import axios from 'axios'
import { localStore } from '@/storage'
import { mapState, mapMutations } from 'vuex'
import Spor from '@/components/modals/strava/Spor'
import strava from 'strava'

console.log(strava, "strava");
let test = new strava({
  client_id:26643,
  client_secret: "f9dd67e081fc52602f9a5330f1e0568305fe7f7c",
  redirect_uri: "http://localhost:8081",
  access_token: "6781698bcf77f63dcf0ea4982d8960f53a8747cd"
})

console.log(test, "Faaaan");

export default {


  data() {
    return {
      refreshData: false,
      lasterMerData: false,
      kartUrl: 'https://kart.skoiddlos.com/test',
      apiUrl:'https://kart.skoiddlos.com/turapp',
      lokalUrl:'http://localhost:8081',
      valgtType: "Alle"
      // apiUrl: 'http://127.0.0.1:5000'
    }
  },
  computed: {
    ...mapState(['kartView', 'stravaSpor']),
    stravaAutorisert: () => {
      return localStore.getItem('auth_code')
    },
    aktivUrl() {
        if (location.hostname === "localhost" || location.hostname === "127.0.0.1") {
          return this.lokalUrl
        } else {
          return this.kartUrl
        }
      },
    valgListe() {
      const liste = ["Alle"]
      if (this.stravaSpor) {
        let liste = ["Alle"]
        let data = [...new Set(this.stravaSpor.map((el) => {
          return el.type
        })) ]
        // console.log(data, "data");
        return liste.concat(data)
      } else {
        return ["Alle"]
      }
    },
    filtrertListe() {
      if (this.stravaSpor) {
        return this.stravaSpor.filter((el) => {
          if (this.valgtType !== "Alle") {
            return el.type === this.valgtType
          } else {
            return true
          }
        })
      } else {
        return []
      }

    }
  },
  components: {
    Spor
  },
  methods: {
    ...mapMutations(['SET_STRAVA', 'SET_STRAVASPOR']),
    oppdaterData() {
      this.SET_STRAVASPOR(null);
      this.refreshData = true
      axios.post(this.apiUrl + '/hentaktiviteter', {"code":this.stravaAutorisert})
      .then((response)=>{
        this.refreshData = false
        this.SET_STRAVASPOR(response.data.data)
      })
      .catch((error)=>{
        this.refreshData = false
        console.log(error, 'Error');
      })
    },
    hentMerData(dato) {
      this.lasterMerData = true
      axios.post(this.apiUrl + '/hentaktiviteter', {"code":this.stravaAutorisert, "dato": this.stravaSpor[this.stravaSpor.length-1].start_date_local})
      .then((response)=>{
        this.lasterMerData = false
        response.data.data.shift()
        this.SET_STRAVASPOR(response.data.data)
        // this.refreshData = false
        // this.stravaSpor response.data.data
      })
      .catch((error)=>{
        this.refreshData = false
        console.log(error, 'Error');
      })
    },
    kafaens() {
      this.refreshData = true
      setTimeout(()=>{this.refreshData = false}, 100)
    }
  },
  mounted() {
    // Last inn data fra spor hvis det ikke allerede er lastet ned
    console.log(process.env.VUE_APP_TEST, "Strava client id")
    console.log(process.env.VUE_APP_STRAVA_CLIENT_ID, "Strava client id")
    console.log(strava, "strava sjiit")

    if (this.stravaAutorisert && !this.stravaSpor) {
      this.oppdaterData()
    }
  }
}
</script>

<style lang="css">
  .mifarge {
    background-color: #e1e9dc !important;
  }

  .mifarge2 {
    background-color: #d2d9ce !important;
  }
</style>
