<template>
  <v-layout row justify-center>
    <v-dialog v-model="innstillinger" scrollable max-width="450px"
    attach="map"
    hide-overlay>
      <v-card class="ramme farge" >
        <v-card-title primary-title color="primary" class="pa-0 ma-0 primary">
          <span class="headline pa-2 font-weight-bold">Innstillinger</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="my-2 px-0 text-xs-left" v-if="atmosfoere">
          <v-layout column wrap>
            <expand aktiv>
              <template slot="header">
                <span class="title font-weight-medium">Generelle innstillinger</span>
              </template>
              <p>
                <v-layout row wrap align-center>
                  <v-flex xs8>
                    <span>Kvalitetsprofil</span>
                  </v-flex>
                  <v-flex xs4>
                    <v-select
                      :items="kvalitetsProfilValg"
                      item-text="tekst"
                      item-value="verdi"
                      v-model="kvalitetsProfil"
                    ></v-select>
                  </v-flex>
                </v-layout>
              </p>
            </expand>
            <expand>
              <template slot="header"><span class="title font-weight-medium">Atmosfære</span></template>
              <p>
                <v-layout row wrap align-center>
                  <v-flex xs8>
                    <span>Aktiv</span>
                  </v-flex>
                  <v-flex xs4>
                    <v-switch
                      v-model="aktivAtmosphere"
                      color="success"
                      hide-details
                    ></v-switch>
                  </v-flex>
                </v-layout>
              </p>
              <p>
                <v-layout row wrap justify-space-between align-center>
                  <v-flex xs6>
                    Atmosfærekvalitet
                  </v-flex>
                  <v-flex xs6>
                    <v-select
                     :items="kvalitetsValgAtmos"
                     v-model="atmosphereQual"
                     item-text="tekst"
                     item-value="verdi"
                   ></v-select>
                  </v-flex>
                </v-layout>
              </p>
            </expand>
          </v-layout>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import {mapState, mapGetters} from 'vuex'
import Expand from '@/components/Expand'

export default {
  data() {
    return {
      kvalitetsValgAtmos: [
        {tekst: 'Lav', verdi: 'low'},
        {tekst: 'Høy', verdi: 'high'}
      ],
      kvalitetsProfilValg: [
        {tekst: 'Lav', verdi: 'low'},
        {tekst: 'Medium', verdi: 'medium'},
        {tekst: 'Høy', verdi: 'high'}
      ],
    }
  },
  components: {
    Expand
  },
  computed: {
    ...mapState(['innstillingerSynlig', 'kartView']),
    ...mapGetters(['environment']),
    innstillinger: {
      set(value) {
        this.$store.commit('SET_INNSTILLINGER', value)
      },
      get() {
        return this.innstillingerSynlig
      }
    },
    aktivAtmosphere: {
      set(value) {
        this.$store.commit('TOGGLE_ATMOSPHERE', value)
      },
      get () {
        return this.environment.atmosphereEnabled
      }
    },
    atmosphereQual: {
      set(value) {
        this.$store.commit('ATMOSPHERE_QUALITY', value)
      },
      get () {
        return this.environment.atmosphere.quality
      }
    },
    kvalitetsProfil: {
      set(value) {
        this.$store.commit('SET_QUALITY', value)
      },
      get () {
        return this.kartView.qualityProfile
      }
    },
    atmosfoere() {
      if (this.kartView !== null) {
        return this.kartView.environment.atmosphere
      } else {
        null
      }
    }
  },
  methods: {
    alert() {
      alert('click')
    }
  }
}
</script>

<style lang="css" scoped>
.aktiv{
  background-color: #e3f5cf !important;
}

.knapp{
  cursor: pointer;
}

.ramme {
  border: 2px solid rgb(166, 170, 164) !important;
  border-radius: 4px !important;
}

.farge {
  background-color: #c6cfc1 !important;
}
</style>
