import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import storageFactory from '@/storageFactory'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    stravaSpor: null,
    verktoySynlig: true,
    modalSynlig: false,
    menyAktiv: false,
    defaultUndermeny: ['Tegnforklaring', 'Bakgrunn', 'Kartlag', 'Verktøy', 'Innstillinger', 'Info', 'Todo'],
    aktivUndermeny: ['Tegnforklaring', 'Bakgrunn', 'Kartlag', 'Verktøy', 'Innstillinger', 'Info', 'Todo'],
    aktivtVerktoy: null,
    aktiveLag: [],
    todoData: null,
    // aktivUndermeny: ['Info'],
    innstillingerSynlig: false,
    inputSynlig: false,
    stravaSynlig: false,
    addataSynlig: false,
    todoAktiv: false,
    aktivtOversiktskart: null,
    baselag: null,
    kartlag: null,
    kartView: null,
    stedsNavn: '',
    aktivKlikkEvent: null,
    grader: 0,
    downloadLink: null,
    solposisjon: {},
    stedsdata: {
      data: {},
      soker: false,
      timeout: false
    },
    verktoy: {
      punktZoom: false,
      maaleAktiv: false,
      velgExtent: false,
      stedsSok: false,
      solsimulering: false,
      avstandsmaaling: false,
      tegneMeny: false,
      tegnForklaring: false
    },
    aktivDraw: null,
    map: {
      heading: 0,
    },
    sketch: null,
  },
  getters: {
    view: state => state.kartView ? state.kartView : null,
    environment: state => state.kartView ? state.kartView.environment : null,
    maale: state => state.verktoy.maaleAktiv,
    kartlagGetter: state => state.kartlag,
    oppdatererGrafikk: state => state.sketch.updateGraphics
  },
  mutations: {
    TOGGLE_MENY: (state, value) => {
      state.menyAktiv = value;
    },

    TOGGLE_VERKTOY: (state, value) => {
      state.verktoySynlig = value;
    },

    SET_AKTIVDRAW: (state, drawobj) => {
      if (drawobj) {
        state.aktivDraw = drawobj
      } else {
        state.aktivDraw = null
      }
    },

    SET_KLIKKEVENT: (state, event) => {
      state.aktivKlikkEvent = event
    },

    SET_STEDSNAVN: (state, value) => {
      state.stedsNavn = value;
    },

    SET_GPXLINK: (state, link) => {
      state.downloadLink = link
    },

    SET_STEDSDATA: (state, value) => {
      if (value) {
        state.stedsdata = value;
      } else {
        state.stedsdata = {
          data: {},
          soker: false,
          timeout: false
        }
      }
    },

    SET_DIALOG: (state, value) => {
      state.modalSynlig = value;
    },

    SET_INNSTILLINGER: (state, value) => {
      state.innstillingerSynlig = value;
    },

    TOGGLE_TODO: (state, value) => {
      state.todoAktiv = value;
    },

    SET_STRAVA: (state, value) => {
      state.stravaSynlig = value;
    },

    SET_ADDATA: (state, value) => {
      state.addataSynlig = value;
    },

    SET_TODODATA: (state, value) => {
      state.todoData = value;
    },

    SET_INPUTSYNLIG: (state, value) => {
      state.inputSynlig = value;
    },

    SET_STRAVASPOR: (state, value) => {
      if (state.stravaSpor === null || value === null) {
        state.stravaSpor = value;
      } else {
        state.stravaSpor.push(...value);
      }
    },

    SET_OVERSIKTSKART: (state, value) => {
      state.aktivtOversiktskart = value;
    },

    SET_VIEW: (state, view) => {
      state.kartView = view;
    },

    SET_SKETCH: (state, sketch) => {
      state.sketch = sketch;
    },

    SET_BASELAG: (state, baselag) => {
      state.baselag = baselag;
    },

    SET_KARTLAG: (state, kartlag) => {
      console.log(kartlag, "Helvetes satanfaen")
      let kartlagObject = {}
      try {
        kartlag.map(el => {
          kartlagObject[el.id] = el
        })
      }
      catch(error) {
        console.error(error);
        // expected output: ReferenceError: nonExistentFunction is not defined
        // Note - error messages will vary depending on browser
      }

      state.kartlag = kartlagObject;
    },

    SET_AKTIVELAG: (state, id) => {
      if (state.aktiveLag.indexOf(id) == -1 ) {
        state.aktiveLag.push(id)
      } else {
        state.aktiveLag.splice(state.aktiveLag.indexOf(id), 1 );
      }
    },

    SET_MAPINFO: (state, payload) => {
      state.map[payload.attribute] = payload.value;
    },

    SET_UNDERMENY: (state, undermeny) => {
      if (state.aktivUndermeny.length === 1 && state.aktivUndermeny[0] === undermeny) {
        state.aktivUndermeny = state.defaultUndermeny;
      } else if (undermeny === 'reset') {
        state.aktivUndermeny = state.defaultUndermeny;
      } else {
        state.aktivUndermeny = [undermeny];
      }
    },

    SET_AKTIVT_VERKTOY: (state, value) => {
      if (value === state.aktivtVerktoy) {
        state.aktivtVerktoy = null
      } else {
        state.aktivtVerktoy = value;
      }
    },

    TOGGLE_TOOL: (state, payload) => {
      console.log(payload);
      Object.keys(payload).map(el => {
        state.verktoy[el] = payload[el]
      })
    },

    TOGGLE_KARTLAG: (state, i) => {
      // console.log(state.kartlag, i, "Toggle kartlag");
      state.kartlag[i].visible = !state.kartlag[i].visible;
    },

    TOGGLE_ATMOSPHERE: (state, value) => {
      state.kartView.environment.atmosphereEnabled = value;
    },

    ATMOSPHERE_QUALITY: (state, value) => {
      console.log(value, "atmoskvalitet");
      state.kartView.environment.atmosphere.quality = value;
    },

    SET_QUALITY: (state, value) => {
      console.log(value, "kvalitet");
      state.kartView.qualityProfile = value;
    },

    SET_GRADER: (state, value) => {
      state.grader = value;
    },

    SET_SOLPOSISJON: (state, payload) => {
      Object.keys(payload).map(el => {
        state.solposisjon[el] = payload[el]
      })
    }

  },
  actions: {
    resetMeny({commit, state}, value) {
      commit('TOGGLE_MENY', value ? value : false);
      commit('SET_UNDERMENY', 'reset');
    },

    resetTools({commit, state}) {
      let payload = {}
      Object.keys(state.verktoy).map(el => {
        payload[el] = null
      })
      commit('TOGGLE_TOOL', payload)
    },

    toggleKartlag({commit}, index) {
      commit('TOGGLE_KARTLAG', index)
    },

    hentData({commit}) {
      axios.get('http://127.0.0.1:5000/api/todo')
      .then((response) => {
        console.log(response)
        const data = response.data.data.map((item) => {
            return {kortnavn: item.kortnavn, id: item.id, date: item.date ? moment(item.creation_date) : null, type: item.type, status: item.status, beskrivelse: item.beskrivelse ? item.beskrivelse : null, merknad: item.merknad ? item.merknad : null, prioritet: item.prioritet}
          })
        commit('SET_TODODATA', data)
      })
      .catch(error => (console.log(error, "hentdata")))
    },

    delItem ({dispatch}, id) {
      console.log(id);
      axios.delete('http://127.0.0.1:5000/api/todo', {
        data: {
          id: id
        }
      })
      .then((response) => {
        console.log(response)
        dispatch('hentData')
      })
      .catch((error) => {
        console.log(error, "post data");
      })
    },

    submitTodo ({dispatch},payload) {
      // Native form submission is not yet supported
      console.log(payload);
      axios.post('http://127.0.0.1:5000/api/todo', {
        "kortnavn": payload.kortnavn,
        "type": payload.type,
        "prioritet": payload.prioritet,
        "beskrivelse": payload.beskrivelse,
        "merknad": payload.merknad ? payload.merknad : null,
        "status": payload.status
      })
      .then((response) => {
        dispatch('hentData')
        console.log(response, "dette er response")
      })
      .catch((error) => {
        console.log(error, "post data");
      })
    },

    updateTodo ({dispatch},payload) {
      // Native form submission is not yet supported
      console.log(payload);
      axios.put('http://127.0.0.1:5000/api/todo', {
        "id": payload.id ? payload.id : null,
        "kortnavn": payload.kortnavn,
        "type": payload.type,
        "prioritet": payload.prioritet,
        "beskrivelse": payload.beskrivelse,
        "merknad": payload.merknad ? payload.merknad : null,
        "status": payload.status
      })
      .then((response) => {
        dispatch('hentData')
        console.log(response, "dette er response")
      })
      .catch((error) => {
        console.log(error, "post data");
      })
    },

    resetKart ({commit, state}) {

    }
  },
  modules: {
  }
})
