<template lang="html">
  <v-layout row wrap>
    <v-card flat class="pa-0 ma-0" color="transparent">
      <v-card-text class="pa-0 ma-0 text-xs-left">
          <v-card flat color="rgb(220, 227, 217)" >
            <v-card-text class="pb-0">
              <article class="" v-if="visintro !== 'nei'">
              <h2>Velkommen</h2>
              <p>Denne applikasjonen ble lagd for å kunne hente inn informasjon og visualisere kartet i 3D</p>
              <p>Om dette er første gang du besøker denne applikasjonen anbefaler jeg å ta en kjapp titt på oversikten under for å få en forståelse for hvordan du bruker applikasjonen</p>
              <p><b class="pr-1">§</b>Dette er en applikasjon som er helt gratis å bruke og som jeg ikke tjener noe penger på, og skal helles ikke bruke i forretningsøyemed</p>
              <p>Alle bilder som blir tatt av denne applikasjonen skal bare brukes privat</p>
              <p>
                Håper du liker den, og om du ikke gjør det så kan du gjerne si fra til meg på <br>
                <v-layout row wrap justify-start align-center class="pa-0 ma-0">
                  <v-icon class="pr-1" color="black">mail</v-icon><a href="mailto:post@skoiddlos.com">post@skoiddlos.com</a>
                </v-layout>
              </p>
              </article>
              <v-checkbox
                @change="oppdaterIntro"
                 label="Vis introduksjon"
                 v-model="introduksjonVises"
               ></v-checkbox>
            </v-card-text>
          </v-card>
        <article class="pb-3">
          <v-layout row wrap align-center>
            <v-icon style="top:5px; position: relative" large class="pr-2">more_horiz</v-icon><h2 class="pb-2 pt-3">Navigasjon</h2>
          </v-layout>
          <p>
            <v-layout row wrap align-center>
              <v-flex xs1>
                <v-icon>gps_fixed</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>vil hjelpe deg å finne ut hvor du er</span>
              </v-flex>
            </v-layout>
          </p>
          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>navigation</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>Viser himmelretning for visualisering. Roterer kartvisningen slik at nord peker opp om du klikker på den</span>
              </v-flex>
            </v-layout>
          </p>
          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>3d_rotation</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>Roterer 360 grader rundt visualiseringens midpunkt</span>
              </v-flex>
            </v-layout>
          </p>
          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>keyboard_backspace</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>Hopper tilbake til forrige visningspunkt. <small><br>Nyttig om kameraet plutselig flyger til helvete i vei et sted</small></span>
              </v-flex>
            </v-layout>
          </p>
          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>places</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>skrur på funksjonalitet for å zoome til et punkt i kartet ved et enkelt trykk. Skrus av/på</span>
              </v-flex>
            </v-layout>
          </p>
        </article>
        <article class="pb-3">
          <v-layout row wrap align-center pb-2>
            <v-icon style="top:5px; position: relative" class="pr-2" large>build</v-icon><h2 class="pb-2 pt-3">Funksjoner</h2>
          </v-layout>
          <p>
            <v-layout row wrap align-center>
              <v-flex xs1>
                <v-icon class="ma-1">fullscreen</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>slår fullskjermvisning av og på</span>
              </v-flex>
            </v-layout>
          </p>

          <!-- TODO: Fiks denne expand-biten slik at den er en komponentsd -->
          <p>
            <expand-lite>
              <template slot="header">
                <v-layout row wrap align-center>
                  <v-flex xs1 >
                    <v-avatar
                      size="30px" class="ma-1">
                      <img
                        :src= "require('@/assets/avalanche.svg')"
                        alt="John"
                      >
                    </v-avatar>
                  </v-flex>
                  <v-flex xs11 pl-2>
                    <span>Hent regObs-observasjoner</span>
                  </v-flex>
                </v-layout>
              </template>
              <div class="">
                <h3>UNDER UTVIKLING</h3>
                <p>Dette verktøyet simulerer solgangen i løpet av en dag, og viser bare et grovt estimat av solgangen</p>
                <p>Klikk på "Velg solgpunkt"-knappen deretter velg det punktet du vil simulerer solgangen på</p>
                <p>Velg deretter dato og terreng-grunnlag og skru på tidsintervallet</p>
                <small>NB: Dette verktøyet er under utvikling og vil undergå store forandringer etterhvert</small>
              </div>
            </expand-lite>
          </p>

          <p>
            <expand-lite>
              <template slot="header">
                <v-layout row wrap align-center>
                  <v-flex xs1>
                    <font-awesome-icon class="ma-1" icon="sun" size="2x" :color="'#616160'"/>
                  </v-flex>
                  <v-flex xs11 pl-2>
                    <span>Aktiverer solsimulerings-meny</span>
                  </v-flex>
                </v-layout>
              </template>
              <div class="">
                <h3>UNDER UTVIKLING</h3>
                <p>Dette verktøyet simulerer solgangen i løpet av en dag, og viser bare et grovt estimat av solgangen</p>
                <p>Klikk på "Velg solgpunkt"-knappen deretter velg det punktet du vil simulerer solgangen på</p>
                <p>Velg deretter dato og terreng-grunnlag og skru på tidsintervallet</p>
                <small>NB: Dette verktøyet er under utvikling og vil undergå store forandringer etterhvert</small>
              </div>
            </expand-lite>
          </p>


          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>camera_alt</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>Tar en skjermdump av nåværende kartvisualisering</span>
              </v-flex>
            </v-layout>
          </p>
          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>straighten</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>Måleverktøy for å måle direktelinje, loddrett stigning og vannrett <br>Viser også kotenivå under pekeren </span>
              </v-flex>
            </v-layout>
          </p>
          <p>
            <v-layout row wrap>
              <v-flex xs1>
                <v-icon>info</v-icon>
              </v-flex>
              <v-flex xs11 pl-2>
                <span>Klikk på kartet for å finne ut informasjon om stedsnavn, høyde samt solgang og vær </span>
              </v-flex>
            </v-layout>
          </p>
        </article>
        <article class="pb-3">
          <v-layout row wrap align-center>
            <v-icon style="top:5px; position: relative" large class="pr-2">search</v-icon><h2 class="pb-2 pt-3">Navigasjon</h2>
          </v-layout>
          <p>Om du skal søke på et sted skriver du det inn i søkemenyen</p>
          <p>Vil du forfine søket kan du skrive fylke, kommune eller by skilt med et komma etter stedsnavnet. <br>Eksempel: <i>Hinnstein, Harstad</i></p>
          <p>Det er ikke mulig å søke på adresser i denne applikasjonen</p>
        </article>
        <article class="pb-3">
            <h2 class="pb-2 pt-3"><v-icon large class="pr-2" color="grey darken-1">directions_bike</v-icon>Strava-spor</h2>
            <p><v-icon class="pr-2" color="grey darken-1">person</v-icon>For å se på dine Strava-aktiviteter må du først autorisere denne kartapplikasjonen til å kunne se alle dine aktiviteter. Både private og offentlige. Autorisasjonen vil bli lagret på din enhet, slik at du slipper å gjenta prosessen hver gang.</p>
            <p><v-icon class="pr-2" color="grey darken-1">vpn_key</v-icon>Selv om du gir denne applikasjonen tilgang til å se disse dataene så får ingen andre tilgang til de, med mindre de logger seg på din enhet og åpner denne kartappen.</p>
            <p><v-icon class="pr-2" color="grey darken-1">delete</v-icon>For å fjerne autorisasjon kan du deautorisere applikasjonen ved å klikke på det. Om du vil se på dataene dine på et senere tidspunkt må du da autorisere app'en igjen.</p>
            <p><v-icon class="pr-2" color="grey darken-1">warning</v-icon>Hvis du har problemer med å laste inn dataene dine, prøv å deautorisere applikasjon og autorisere den på nytt igjen da det kan oppstå noe problemer med tilgangen.</p>
        </article>
      </v-card-text>
    </v-card>
  </v-layout>
</template>

<script>
import { localStore } from '@/storage'
import ExpandLite from '@/components/helpercomponents/ExpandLite'

export default {
  data() {
    return {
      introduksjonVises: true,
      visintro: this.visIntro
    }
  },
  components: { ExpandLite },
  computed: {
    visIntro() {
      return localStore.getItem('visintro')
    },
  },
  methods: {
    oppdaterIntro() {
      if (!this.introduksjonVises) {
        localStore.setItem('visintro', 'nei')
        this.visintro = localStore.getItem('visintro')
      } else {
        localStore.setItem('visintro', 'ja')
        this.visintro = localStore.getItem('visintro')
      }
    }
  },
  destroyed() {
    if (!this.introduksjonVises) {
      localStore.setItem('visintro', 'nei')
    }
  }
}
</script>

