<template>
    <v-navigation-drawer
      v-model="menySynlig"
      absolute
      temporary
      :touchless = "!menyAktiv"
      flat
      class="sidemeny xs8"
      width = "350"
      height = "100%"
      hide-overlay
    >
    <v-container grid-list-md class="pa-0 ma-0 meny" fill-height justify-start align-start>
      <v-card
        class="pa-0 ma-0"
        height="100%"
        width="350"
        color="transparent"
        fluid
        tile
        flat
        >
        <v-card-text class="pa-0 ma-0" max-height="95vh">
          <v-layout column wrap>
            <v-card color="transparent" flat tile fill-height fluid>
              <v-card-title primary-title class="pa-1">
                <v-layout row wrap>
                  <v-flex class="text-xs-left display-1 font-weight-bold" pa-2 align-center>
                    <v-avatar
                      size="30"
                      tile
                      class="px-2"
                    >
                    <v-img
                      pa-1
                      :src= "require('@/assets/fjell.svg')"
                      alt="alt" contain></v-img>
                    </v-avatar>
                    <span class="font-weight-thin noselect">Turur</span>
                  </v-flex>
                </v-layout>
              </v-card-title>
              <v-card-text class="noselect ma-0 pa-0">
                <v-layout column wrap align-start>

                  <!-- Bakgrunnskart -->
                  <!-- <expand skjulpil v-if="aktivUndermeny.includes('Bakgrunn')" id="Bakgrunn"> -->
                  <expand skjulpil kartlag v-if="aktivUndermeny.includes('Bakgrunn')" id="Bakgrunn" class="px-2">

                    <template slot="header">
                      <v-layout row no-wrap justify-space-between class="text-xs-left gruppe-tittel">
                        <v-flex class="">
                          <span class="font-weight-thin " >Bakgrunn:</span>
                        </v-flex>
                        <v-flex class="pr-1 text-xs-left">
                          <span class="px-2 font-weight-bold text-truncate"> {{ aktivBakgrunn }}</span>
                        </v-flex>
                      </v-layout>
                    </template>
                    <div class="text-xs-left gruppe-innhold" v-for="i in baselag">
                      <div @click="velgBaseLag(i.id)" :class="[i.id === aktivBakgrunn ? 'aktiv-bakgrunn' : 'ikke-aktiv', 'py-1', 'subtitle']">{{ i.id }}</div>
                    </div>
                  </expand>

                  <!-- Kartlag -->
                  <expand skjulpil  kartlag v-if="aktivUndermeny.includes('Kartlag')" id="Kartlag" class="px-2">
                    <template slot="header" >
                      <!-- <v-flex > -->
                        <div class="title font-weight-thin gruppe-tittel">
                          Kartlag
                        </div>
                        <!-- <span class="title font-weight-thin gruppe-tittel">Kartlag</span> -->
                      <!-- </v-flex> -->
                    </template>
                    <div class="text-xs-left gruppe-innhold" v-for="i,index in kartlag">
                      <span @click="velgKartLag(index)" :class="[kartlag[index].visible ? 'aktiv-bakgrunn' : 'ikke-aktiv', 'subtitle']">{{ i.id }}</span>
                    </div>
                  </expand>

                  <expand skjulpil kartlag  v-if="aktivUndermeny.includes('Tegnforklaring')" id="Tegnforklaring" class="px-2">
                    <template slot="header" >
                          <span class="title font-weight-thin gruppe-tittel">Tegnforklaring</span>
                    </template>
                    <div class="text-xs-left gruppe-innhold">

                      <table>
                        <div class="">
                            <span class="subtitle">Helling (<span class="small">grader</span>)</span>
                        </div>

                        <tbody>
                          <tr valign="middle" v-for="item in helling">
                            <td style="width:30px;"><img style="width:20px; height:20px" :src="item.src" :alt="item.navn"></td>
                            <td class="subheading">{{ item.navn }} </td>
                        </tr>
                        </tbody>
                      </table>

                    </div>

                  </expand>
                  <!-- Verktøy -->
                  <expand skjulpil kartlag v-if="aktivUndermeny.includes('Verktøy')" id="Verktøy" class="px-2">
                    <template slot="header">
                          <span class="title font-weight-thin gruppe-tittel" >Verktøy</span>
                    </template>
                    <verktoy  v-for="i in verktoy" v-bind="i" :key="i.id">
                      <template slot="header" >
                            <span class="" >{{ i.id }}</span>
                      </template>
                    </verktoy>
                    <!-- <div class="text-xs-left gruppe-innhold" v-for="i in verktoy">
                      <span class="disabled py-1">{{ i }}</span>
                    </div>
                    <div class="text-xs-left gruppe-innhold" @click="toggleStrava">
                      <span>Strava-spor</span>
                    </div>
                    <div class="text-xs-left gruppe-innhold" @click="delkart">
                      <span>Del kart</span>
                    </div> -->
                  </expand>

                  <!-- Innstillinger -->
                  <div class="text-xs-left gruppe-el pl-3" @click="toggleInnstillinger" v-if="aktivUndermeny.includes('Innstillinger')">
                    <span class="title font-weight-thin gruppe-tittel " >Innstillinger</span>
                  </div>

                  <!-- Info -->
                  <div class="text-xs-left gruppe-el pl-3" @click="toggleModal" v-if="aktivUndermeny.includes('Info')">
                    <span class="title font-weight-thin gruppe-tittel " >Info</span>
                  </div>

                  <!-- <expand skjulpil kartlag v-if="aktivUndermeny.includes('Todo')" id="Todo" class="px-2">
                    <template slot="header">
                          <span class="title font-weight-thin gruppe-tittel" ><v-icon>add</v-icon>Todo</span>
                    </template>
                    <v-layout column wrap>
                      <v-btn color="primary" @click="TOGGLE_TODO(true)">Legg til</v-btn>
                      <v-btn color="primary" @click="TOGGLE_TODO(true)">Se på</v-btn>
                    </v-layout>
                  </expand> -->

                </v-layout>
              </v-card-text>
            </v-card>
          </v-layout>
        </v-card-text>
      </v-card>

    </v-container>
    </v-navigation-drawer>
</template>

<script>
  import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
  import { queryStrParse } from '@/mixins/queryStringParse'
  import { mapTools } from '@/mixins/maptools'

  import Expand from '@/components/Expand'
  import Verktoy from './Verktoy'




  export default {
    data () {
      return {
        aktivBakgrunn: 'Flyfoto',
        aktiveLag: [],
        // lag: ['Helling', 'Turer', 'Blabla'],
        verktoy: [
          {id: 'Strava-spor', beskrivelse: null, disabled: false, method: this.toggleStrava},
          {id: 'Del kart', beskrivelse: null, disabled: false, method: this.delkart},
          {id: 'Legg til data', beskrivelse: null, disabled: false, method: this.toggleAddData},
        ],
        helling: [
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADZJREFUOI1jYaAyYKGlgf+pYB4jugsZKTDsPwMDjb08auCogaMGjhqI00BKykRGdAMpKQvhAABd0QNSDPGCHwAAAABJRU5ErkJggg==","navn":"0 - 27"},
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADpJREFUOI1jYaAyYKGlgf+pYB4jigtVSsg36U4PhKapl0cNHDVw1MBRA7EbCCuCyASM6AYyUmQcFAAA2w4Eu02XbecAAAAASUVORK5CYII=","navn":"27 - 30"},
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADhJREFUOI1jYaAyYKGlgf+pYB4jigv/U2AkIyOEpqmXRw0cNXDUwFEDsRsIK4LIBIzoBlJmHBQAAF8jA1XJU6ZXAAAAAElFTkSuQmCC","navn":"30 - 35"},
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADlJREFUOI1jYaAyYKGlgf+pYB4jigv/r6TApHAITVMvjxo4auCogaMGYjcQVgSRCRjRDWSkyDgoAAB8swOrTPh0KQAAAABJRU5ErkJggg==","navn":"35 - 40"},
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADlJREFUOI1jYaAyYKGlgf+pYB4jigv/h1Jg0moITVMvjxo4auCogaMGYjcQVgSRCRjRDWSkyDgoAACZkwP/r1UNTQAAAABJRU5ErkJggg==","navn":"40 - 45"},
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADhJREFUOI1jYaAyYKGlgf+pYB4jigufUWCSFJSmqZdHDRw1cNTAUQOxGyiFSxVxgBHdQEbKzIMAABQxAm3yqYmFAAAAAElFTkSuQmCC","navn":"45 - 50"},
            {"src":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAAAXNSR0IB2cksfwAAAAlwSFlzAAAOxAAADsQBlSsOGwAAADhJREFUOI1jYaAyYKGlgf+pYB4jigtLKDCpB0rT1MujBo4aOGrgqIHYDezBpYo4wIhuICNl5kEAADvTAt94zGSEAAAAAElFTkSuQmCC","navn":"50 - 90"}
          ]
      }
    },
    components: {
      Expand, Verktoy
    },
    computed: {
      ...mapState(['menyAktiv', 'kartView', 'baselag', 'kartlag', 'aktivUndermeny', 'aktiveLag']),
      ...mapGetters(['kartlagGetter']),
      menySynlig: {
        set(value) {
          // this.$store.commit('TOGGLE_MENY', value)
          this.$store.dispatch('resetMeny', value)
        },
        get() {
          return this.menyAktiv
        },
      },
      gjemOverlay() {
        return this.aktivUndermeny === 'Tegnforklaring'
      },
      lokalHost() {
        if (location.hostname === "localhost" || location.hostname === "127.0.0.1") {
          return true
        }
      }
    },
    mixins: [queryStrParse, mapTools],
    methods: {
      ...mapMutations(['SET_KARTLAG', 'SET_DIALOG', 'SET_INNSTILLINGER', 'TOGGLE_MENY', 'SET_STRAVA', 'SET_ADDATA', 'SET_UNDERMENY', 'TOGGLE_TODO', 'SET_AKTIVELAG']),
      ...mapActions(['resetMeny', 'hentData', 'toggleKartlag']),
      delkart() {
        let url = this.delKart(this.kartView.camera)
        prompt("Kopier og del kartlenke", url)
        console.log(url);
      },
      velgBaseLag(id) {
        this.aktivBakgrunn = id
        this.kartView.endreBakgrunn(id)
      },
      // TODO: Gjør dette bedre. Det er ikk reaktivt og hører ikke etter lagene i kart-objektet
      velgKartLag(i) {
        console.log(this.kartlag, "Skjønna itj nå")
        this.SET_AKTIVELAG(i)
        this.toggleKartlag(i)
        // this.TOGGLE_KARTLAG(i)
      },
      // oppdaterLagliste(id) {
      //   if (this.aktiveLag.indexOf(id) == -1 ) {
      //     this.aktiveLag.push(id)
      //   } else {
      //     this.aktiveLag.splice( this.aktiveLag.indexOf(id), 1 );
      //   }
      // },
      toggleInnstillinger() {
        this.SET_INNSTILLINGER(true)
        this.TOGGLE_MENY(false)
      },
      toggleModal() {
        this.SET_DIALOG(true)
        this.TOGGLE_MENY(false)
      },
      toggleStrava() {
        this.SET_STRAVA(true)
        this.TOGGLE_MENY(false)
      },
      toggleAddData() {
        this.SET_ADDATA(true)
        this.TOGGLE_MENY(false)
      }
    },
    created() {
      let sok = this.parseAuthCode()
      this.hentData()
      console.log(this.parseAuthCode(), 'querystring');
    }
  }
</script>

<style lang="css" scoped>
.meny{
  background-color: #c6cfc1;
  border-right: 2px solid #8f988b;
}

.gruppe-el{
  padding:6px 6px;
  width: calc(100% - 2px);
  transition: all .3s;
}

.gruppe-el:hover {
  background-color: rgb(232, 229, 229)
}

.disabled {
  opacity: .5;
  cursor: not-allowed;
}

.aktiv-bakgrunn {
  color: black;
}

.ikke-aktiv {
  font-weight: lighter;
  font-style: italic;
  color: rgb(163, 163, 163);
}

.gruppe-tittel{
  font-size: 2em !important;
  cursor: pointer
}

.gruppe-innhold {
  font-size: 1.5em !important;
}

.sidemeny {
  overflow: hidden;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}
</style>
