<template lang="html">
  <v-layout column wrap class="text-xs-left">
    <v-flex>
      <p>Kopier inn en streng av den google enkoda-polylinjen du vil dekode og trykk "dekode", så kan du se et oversiktskart over linja i kartet</p>
      <p>Om du vil legge til linja i hovedkartet velger du "Legg til spor"</p>
      <v-text-field
        v-model="polyline"
        name="name"
        label="Legg inn polylinje"
        id="id"
        @click="polyline=''"
      ><v-icon>close</v-icon></v-text-field>
      <v-layout row wrap justify-center>
          <v-btn v-if="!decode" color="primary" @click="decode = true">Dekode</v-btn>
          <v-btn v-else color="primary" @click="reset">Reset</v-btn>
      </v-layout>
      <v-card color="#d7e3cd" class="my-2" v-if="decode">
        <v-card-text>
          <leafmap v-if="decode" :linje="polyline"></leafmap>
          <v-btn color="primary" @click="leggTilSpor">Legg til spor</v-btn>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>

</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import decode from '@/assets/js/decodePolyline'

import Leafmap from '@/components/modals/Leafmap'

export default {
  data() {
    return {
      decode: false,
      polyline: 'co|bLce~cB{@fAaAfBaEtA{CKuDeAiC{@k@LgBbLFrB'
    }
  },
  computed: {
    ...mapState(['kartView'])
  },
  components: { Leafmap },
  methods: {
    ...mapMutations(['SET_ADDATA']),
    reset() {
      this.polyline = ''
      this.decode = false
    },
    leggTilSpor() {
      if (this.polyline) {
        let linje = decode(this.polyline).map(el => {
          return [el.longitude, el.latitude]
        })
        // console.log(linje);
        this.gpxFil = ''
        this.SET_ADDATA(false)
        this.kartView.leggTilSegment(linje)
      }
    }
  }
}
</script>

