<template lang="html">
  <div class="test"
  v-touch="{
    right: () => swipe('Right'),
  }"
  >
    <header @click="openComp" :class="[{'aktiv': expandAktiv}, 'tov']">
      <v-layout row wrap justify-space-between align-center>
        <slot name="header" class="tov"></slot><span v-if="!skjulpil"><v-icon v-if="!expandAktiv">expand_more</v-icon><v-icon v-if="expandAktiv">expand_less</v-icon></span>
      </v-layout>
    </header>
    <transition name="fadeHeight" mode="out-in">
      <article class="content" v-if="expandAktiv">
        <slot></slot>
      </article>

    </transition>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  data() {
    return {
    }
  },
  computed: {
    ...mapState(['aktivUndermeny', 'aktivMeny']),
    expandAktiv() {
      return this.aktivUndermeny.length === 1 && this.aktivUndermeny.includes(this.id)
    }
  },
  props: {
    aktiv: {
      type: Boolean
    },
    skjulpil: {
      type: Boolean
    },
    id: {
      default: null,
      type: String
    }
  },
  methods: {
    ...mapMutations(['SET_UNDERMENY']),
    openComp() {
      // console.log(this.id);
      this.SET_UNDERMENY(this.id)
    },
    swipe(direction) {
      this.SET_UNDERMENY('reset')
    }
  }
}
</script>

<style lang="css" scoped>
.fadeHeight-enter-active{
  transition: all 0.2s;
  animation: fadelate 0.2s;
}
.fadeHeight-leave-active {
  transition: all 0.2s;
  max-height: 300px;
  /* animation: fadelate 0.2s; */
}
.fadeHeight-enter,
.fadeHeight-leave-to
{
  opacity: 0;
  max-height: 0px;
}
header {
  padding:10px 10px;
  cursor: pointer;
  transition: all .5s;
}

article {
  padding:10px 10px;
  border-left: 10px solid #abb4a6
}
header:hover {
  background-color: rgb(232, 229, 229)
}
.content{
  overflow:hidden;
}
.test{
  transition: transform .4s;
  width: calc(100% - 2px);
}
.aktiv {
  background-color: #abb4a6;
  /* border-top: 1px solid #757972 */
}
@keyframes fadelate {
    0%   {opacity: 0;}
    90%  {opacity: 30;}
    100% {opacity: 100;}
}


</style>
