<template lang="html">
  <!-- <div id="upperRight" v-if="!lagraGrafikk"> -->
  <div id="upperRight" v-show="verktoySynlig">
    <transition name="fade" mode-out="out-in">
      <button type="button" name="undo" @click="verktoyAktiv=!verktoyAktiv" :class="{aktiv: verktoyAktiv}" v-if="!verktoyAktiv"><v-icon>more_horiz</v-icon></button>
      <button type="button" name="undo" @click="verktoyAktiv=!verktoyAktiv" :class="{faded: verktoyAktiv}" v-if="verktoyAktiv"><v-icon>close</v-icon></button>
    </transition>
    <transition name="fade" mode-out="out-in">
      <div id="toolbox" v-if="verktoyAktiv">
          <div class="knapperad right">
            <v-tooltip bottom >
                <button slot="activator" type="button" @click="TOGGLE_TOOL({'punktZoom': !verktoy.punktZoom})" :class="{'aktiv-knapp': verktoy.punktZoom}"><v-icon :color=" verktoy.punktZoom ? 'red lighten-3' : ''">place</v-icon></button>
                <span>Zoom til punkt (av/på)</span>
            </v-tooltip>
            <v-tooltip bottom>
              <button slot="activator" type="button" name="undo" @click="$parent.lokaliserVM.locate()"><v-icon>gps_fixed</v-icon></button>
              <span>Finn meg</span>
            </v-tooltip>
            <button type="button" @click="kartView.roterRundt(true)"><v-icon>3d_rotation</v-icon></button>
          </div>

        <div class="knapperad down">
          <div id="zoom">
            <button type="button" name="zoom-in" @click="$parent.kartView.zoom++"><v-icon>add</v-icon></button>
            <button type="button" name="zoom-out" @click="$parent.kartView.zoom--"><v-icon>remove</v-icon></button>
            <v-tooltip left>
              <button slot="activator"  type="button" name="zoom-out" @click="resetNord"><v-icon class="ingenanimasjon" :style="'transform: rotate('+ map.heading +'deg)'">navigation</v-icon></button>
              <span>Vend nord. Retning: {{ Math.round(map.heading) }} ° {{ himmelretning }}</span>
            </v-tooltip>
            <button type="button" :class="{'flashit': animate}" @click="tilbake"  @animationend=" animate=false" v-if="$parent.cameraList.length > 0"><v-icon>keyboard_backspace</v-icon></button>
            <!-- <v-card>
              {{map.heading}}
            </v-card> -->
          </div>
        </div>
      </div>
    </transition>
    <v-snackbar
      v-model="snackbar"
      bottom
      multi-line
      :timeout="20000"
      color="rgba(124, 175, 128, 0.44)"
    >
    <v-layout row wrap justify-center align-center >
      <v-btn hover color="rgb(117, 142, 113)" class="pa-3 ma-0"><a class="" target="#" :href="bildeData" download="map_screenshot.png"@click="snackbar = false">Last ned bildet</a></v-btn>

    </v-layout>
    </v-snackbar>
  </div>

</template>

<script>
import axios from 'axios'
import {mapState, mapMutations, mapGetters} from 'vuex'
// import {verktoyMixin} from '../../verktoyMixin'

export default {
  // mixins: [verktoyMixin],
  props: ['view', 'lukkMeny'],
  data() {
    return {
      verktoyAktiv: false,
      zoomAktiv: false,
      bildeData: null,
      snackbar: false,
      animate: false
    }
  },
  components: {
  },
  computed: {
    ...mapState(['verktoySynlig', 'innstillingerSynlig', 'modalSynlig', 'kartView', 'verktoy', 'map']),
    menyAapen () {
      if (this.lukkMeny) {
        this.verktoyAktiv = false;
      }
    },
    lagraGrafikk() {
      if (this.$store.state.grafikk) {
        return true
      } else {
        return false
      }
    },
    modalAktiv: {
      get () {return this.modalSynlig},
      set(val) {this.$store.commit('SET_DIALOG', val)}
    },
    innstillingerAktiv: {
      get () {return this.innstillingerSynlig},
      set(val) {this.$store.commit('SET_INNSTILLINGER', val)}
    },
    uiActive: {
      get() {return this.$store.state.uiActive},
      set(val) {this.$store.commit('oppdaterUiActive', val)}
    },
    himmelretning() {     //can be 0 - 360
        var azimuthHumans = [
                  'Nord','Nord-Øst',
                  'Øst','Sør-Øst',
                  'Sør','Sør-Vest',
                  'Vest','Nord-Vest','Nord'];

        return azimuthHumans[ Math.round(this.map.heading/45) ];
    }
  },
  methods: {
    ...mapMutations(['SET_DIALOG', 'SET_INNSTILLINGER', 'TOGGLE_TOOL']),
    toggleTool(toolInst, valueInst) {
      this.TOGGLE_TOOL({toolInst, valueInst})
    },
    transformerKoord (x, y, fra, til) {
      const url = "https://ws.geonorge.no/transApi?";
      let options = {
       params: {
         ost: x,
         nord: y,
         fra: fra,
         til: til
       }
     };
     return axios.get(url, options);
   },
   test() {
     alert(this.uiActive.get())
   },
   faens() {
     this.kartView.maaleLinje()
   },
   resetNord() {
     this.kartView.goTo({heading:0})
   },
   zoomTo(event) {
     this.zoomAktiv = true;
     const pos = this.kartView.toMap(event.clientX, event.clientY)
   },
   taBilde() {
     const vm = this
     this.kartView.takeScreenshot({})
     .then(function(screenshot) {
       vm.bildeData = screenshot.dataUrl;
       vm.snackbar = true
      });
   },
   tilbake() {
     this.animate = true
     this.kartView.goBack()
   },
   animate() {
      this.animated = true
    }
 },
 mounted() {
 }
}
</script>

<style lang="css" scoped>
#upperRight{
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
  box-sizing: border-box;
}

.knapperad{
  margin: 2px;
  position: relative;
}

.right {
  position: absolute;
  top: -2px;
  right: 42px;
  display: flex;
  flex-flow: row nowrap;
  /* float: right; */
  align-items: flex-end;
  justify-content: flex-start;
  flex-direction: row-reverse;
}

.down {
  position: absolute;
  top: 40px;
  left: -2px;
  display: flex;
  flex-flow: column nowrap;
}

#zoom button{
  margin-bottom: 2px;
}

a{
  text-decoration: none;
  color: black
}

button{
  color: rgb(133, 133, 133);
  margin: 0 2px;
  height: 40px;
  width: 40px;
  border: 2px solid #a6aaa4;
  border-radius: 4px;
  background-color: #c5cfc0;
  transition: background-color 0.1s ease-in;
  cursor: pointer;
}

button:hover , .aktiv{
  background-color: #d4d5d3
}

button:active {
  background-color: #ffffff
}

button:focus{
  outline: 0
}

.right{
  justify-content: flex-end;
}

.fade-enter-active, .fade-leave-active {
transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.faded {
  opacity: 0.7;
}

.disabled {
  opacity: .5;
  cursor: not-allowed;
}

.aktiv-knapp {
  background-color: #a4aa9f;
  border-color: #575955 !important
}

.ingenanimasjon {
  transition: none !important;
}

.flashit{
	-webkit-animation: flash linear .3s;
	animation: flash linear .3s;
}

@-webkit-keyframes flash {
	0% { opacity: 1; }
	50% { opacity: .1; }
	100% { opacity: 1; }
}
@keyframes flash {
	0% { opacity: 1; }
	50% { opacity: .1; }
	100% { opacity: 1; }
}

@media (max-width: 495px) {
  #upperRight{
    top: 3px;
    right: 3px;
  }
}
</style>
