<template lang="html">
  <div>
    <header @click="openBeskrivelse" :class="[{disabled: disabled},{aktiv:contentAktivt},'headline','text-xs-left','py-1','px-0','pl-1','ma-0']">
        <slot name="header" class="tov"></slot>
    </header>
    <transition name="fadeHeight" mode="out-in">
      <article class="text-xs-left px-3 mb-2 ma-0 font-italic py-1" v-if="contentAktivt">
        <div class="redicon"></div>
        <span class="grey--text text--darken-1 subheading">{{ beskrivelse }}</span>
      </article>
    </transition>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  data() {
    return {
    }
  },
  computed: {
    ...mapState(['aktivtVerktoy', 'aktivMeny']),
    expandAktiv() {
      return this.aktivtVerktoy === this.id
    },
    contentAktivt() {
      return this.expandAktiv === true && this.beskrivelse !== null && !this.disabled
    }
  },
  props: {
    aktiv: {
      type: Boolean
    },
    id: {
      default: null,
      type: String
    },
    beskrivelse: {
      default: null,
      type: String
    },
    disabled: {
      type: Boolean
    },
    method: {
      default: null,
      type: Function
    }
  },
  watch: {
    aktivMeny: (value) => {
      console.log(value, "Watcher");
    }
  },
  methods: {
    ...mapMutations(['SET_AKTIVT_VERKTOY']),
    openBeskrivelse() {
      if (this.method) {
        this.method()
      } else {
        // console.log('Ingen metode');
      }
      this.SET_AKTIVT_VERKTOY(this.id)
    },
  }
}
</script>

<style lang="css" scoped>
.fadeHeight-enter-active{
  transition: all 0.2s;
  animation: fadelate 0.2s;
}
.fadeHeight-leave-active {
  transition: all 0.2s;
  max-height: 300px;
  /* animation: fadelate 0.2s; */
}
.fadeHeight-enter,
.fadeHeight-leave-to
{
  opacity: 0;
  max-height: 0px;
}
.redicon{
  position: absolute;
  top: -5px;
  left: -6px;
  background: rgb(136, 136, 136);
  padding:5px;
  box-sizing: border-box;
  border-radius: 100%;
}
header {
  padding:10px 10px;
  cursor: pointer;
  transition: all .5s;
}

article {
  border-left: 2px solid rgb(136, 136, 136);
  border-top: 2px solid rgb(136, 136, 136);
  background-color: rgb(208, 210, 207);
  position: relative;
}
/* header:hover {
  background-color: rgb(232, 229, 229)
} */
.test{
  transition: transform .4s;
  width: 100%;
}
.aktiv {
  background-color: #c2c9bc;
  /* border-left: 2px solid rgb(136, 136, 136); */
  /* border-top: 1px solid #757972 */
}
.disabled {
  opacity: .5;
  cursor: not-allowed;
}
@keyframes fadelate {
    0%   {opacity: 0;}
    90%  {opacity: 30;}
    100% {opacity: 100;}
}


</style>
