<template lang="html">
  <v-layout column wrap class="text-xs-left">
    <v-flex>
      <article class="">
        <p>Her kan du laste opp en gpx-fil for å visualisere den.</p>
        <p>Fungerer så langt bare med spor, ikke punkt enda. Det er også bare geometrien som blir lastet opp.</p>
        <p>Støtte for å vise punkt og attributtinformasjon vil komme.</p>
      </article>
      <article class="">
        <h3>Velg gpx-filen du vil vise i kartet</h3>
        <v-layout row wrap>
          <v-flex xs6>
            <v-btn class="mb-3" @click='velgFil' color="primary"><span v-if="!gpxFil">Velg fil</span><span v-else>Velg ny fil</span></v-btn>
            <input
              type="file"
              style="display: none"
              ref="gpxFil"
              accept=".gpx"
              @change="onFilePicked"
            >
          </v-flex>
          <!-- <button class="btn">Last opp ditt spor</button>
          <input type="file" name="myfile" id="upload-file" @change="leggTilFil"/> -->

        </v-layout>
      </article>
      <article class="valgt px-2" v-if="gpxFil">
        <h3 v-if="gpxFil" class="py-2">Filnavn: <span class="subheading" v-if="filNavn"> {{ filNavn }}</span></h3>
        <leafmap :linje="gpxFil" :start="start" :finish="finish"></leafmap>
        <v-layout row wrap justify-center>
          <v-btn class="my-2" v-if="gpxFil" color="primary" @click="leggTilSpor">Legg til spor</v-btn>
        </v-layout>
      </article>
    </v-flex>
  </v-layout>

</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import Leafmap from '@/components/modals/Leafmap'

export default {
  data: () => ({
    title: "Image Upload",
    dialog: false,
    filNavn: '',
    gpxFil: ''
    }),
  computed: {
    ...mapState(['kartView']),
    start() {
      return this.gpxFil[0]
    },
    finish() {
      return this.gpxFil[this.gpxFil.length - 1]
    }
  },
  components: {Leafmap},
  methods: {
    ...mapMutations(['SET_ADDATA']),
    velgFil() {
      this.gpxFil = null
      this.$refs.gpxFil.click()
    },
    onFilePicked (e) {
      const files = e.target.files
      if(files[0] !== undefined) {
        this.filNavn = files[0].name
        if(this.filNavn.lastIndexOf('.') <= 0) {
          return
        }
        const fr = new FileReader ()
        fr.readAsText(files[0])
        fr.addEventListener('load', () => {
          // console.log(fr);
          let gpxData = this.parseGpx(fr.result)
          // this.imageUrl = fr.result
          this.gpxFil = gpxData // this is an image file that can be sent to server...
        })
      } else {
        this.filNavn = ''
        this.gpxFil = ''
        this.imageUrl = ''
      }
    },
    parseGpx (string) {
      var parser = new DOMParser();
      var doc = parser.parseFromString(string, "application/xml");

      var linjePkt = doc.querySelectorAll('trkpt')
      var segmentListe = Array.prototype.map.call(linjePkt, function (obj) {
        // return {
        //   longtitude: parseFloat(obj.attributes.lon.nodeValue),
        //   latitude: parseFloat(obj.attributes.lat.nodeValue)
        // }
        return [parseFloat(obj.attributes.lat.nodeValue), parseFloat(obj.attributes.lon.nodeValue)]
      })
      return segmentListe
    },
    leggTilSpor() {
      if (this.gpxFil) {
        let segment = this.gpxFil.map((el) => {
          return [el[1], el[0]]
        })
        console.log(segment);
        this.gpxFil = ''
        this.SET_ADDATA(false)
        this.kartView.leggTilSegment(segment)
      }
    }
  }
}
</script>

<style lang="css" scoped>
.fil-input {
  cursor: pointer !important;
}

.valgt {
  /* border-left: 4px rgb(97, 125, 97) solid; */
  background-color: rgb(210, 219, 208) !important;
}
</style>
