<template lang="html">
  <v-card id="sol">
    <v-card-text class="pa-3 ma-0">
      <v-layout row wrap justify-end v-if="valgtpkt">
        <v-btn class="pa-0 ma-0" icon color="primary" @click="open=!open" v-if="!open"><v-icon>keyboard_arrow_up</v-icon></v-btn>
        <v-btn class="pa-0 ma-0" icon color="primary" @click="open=!open" v-if="open"><v-icon>keyboard_arrow_down</v-icon></v-btn>
      </v-layout>
      <transition name="fadeHeight">
        <v-layout column wrap align-start justify-center class="pa-0 ma-0" v-if="open">
          <v-btn color="primary" @click="velgPkt">Velg solpunkt</v-btn>
          <article class="text-xs-left" v-if="valgtpkt">
            <h3>Dato</h3>
            <v-layout row wrap justify-start align-center id="test">
              <v-menu
              v-model="menu"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-btn color="primary" icon slot="activator" class="pl-0 ml-0"><v-icon>event</v-icon></v-btn>
              <v-date-picker v-model="valgtdato" @change="settDato"></v-date-picker>
            </v-menu>
              <span class="subheading"> {{ valgtdato }} </span>
            </v-layout>

            <!-- <span>{{ dato }}</span> -->
            <h3>Velg høydemodell</h3>
            <v-layout row wrap>
              <v-btn color="primary" v-if="!overflateAktiv" @click="toggleOverflate">Overflate</v-btn>
              <v-btn color="primary" v-else @click="toggleOverflate">Bakke</v-btn>
            </v-layout>

            <div class="pa-2 text-xs-center">
              <!-- <span>{{ dato }}</span> -->
              <h3 class="text-xs-left">Velg tidspunkt på dagen</h3>
              <span class="subheading ">{{ time | twoDigits}} : {{minutter | twoDigits}}</span>
              <v-layout row wrap justify-center>
                <span class="subheading pr-3" style="position:relative;top:5px">0</span>
                <v-slider
                class="pa-0 ma-0"
                  v-model="tid"
                  :max="solnedgang"
                  :min="soloppgang"
                  hint="Velg tidspunkt på dagen du vil simulere"
                ></v-slider>
                <span class="subheading pl-3" style="position:relative;top:5px">24</span>
              </v-layout>
            </div>
            <div class="">
              <p>Vertikalvinkel: {{ vertikalvinkel }}</p>
              <p>Azimuth: {{ solAzimuth }}</p>
            </div>

            <v-layout row wrap>
              <v-btn color="primary" @click="animerSol" v-if="!solanimering">Animer solgang</v-btn>
              <v-btn color="primary" @click="stoppAnimerSol" v-if="solanimering">Stopp</v-btn>
            </v-layout>
          </article>
        </v-layout>
      </transition>
    </v-card-text>
  </v-card>
</template>

<script>
import {mapState, mapMutations} from 'vuex'

export default {
  data() {
    return {
      open: true,
      visKoord: false,
      solanimering: null,
      tid: 720,
      valgtdato: null,
      valgtpkt: false,
      dato: null,
      menu: false,
      solinfo: null,
      solplassering: null,
      overflateAktiv: false
    }
  },
  computed: {
    ...mapState(['kartView', 'solposisjon']),
    tidReell() {
      return (this.tid - this.tid % 60) / 60 + ":" + this.tid % 60
    },
    formatertDato() {
      return this.dato.getDate() + '/' + (this.dato.getMonth()+1) + '/' + this.dato.getFullYear()
    },
    tidssone() {
      return this.dato.getTimezoneOffset()
    },
    klokkeslett() {
      return this.dato.getHours() + ':' + this.dato.getMinutes()
    },
    grader: {
      get() {return this.$store.state.grader},
      set(val) {this.$store.commit('SET_GRADER', val)}
    },
    time() {
      return (this.tid - this.tid % 60) / 60
    },
    minutter() {
      return this.tid % 60
    },
    valgtDatoArr() {
      return this.valgtdato ? this.valgtdato.split('-') : null
    },

    solnedgang() {
      if (this.solinfo) {
        return this.solinfo.sunset.dato.toString() !== "Invalid Date" ? this.solinfo.sunset.dato.getHours() * 60 + this.solinfo.sunset.dato.getMinutes() : 1440
      } else {
        return 1440
      }


    },
    soloppgang() {
      if (this.solinfo) {
            return this.solinfo.sunrise.dato.toString() !== "Invalid Date" ? this.solinfo.sunrise.dato.getHours() * 60 + this.solinfo.sunrise.dato.getMinutes() : 0
      } else {
        return 0
      }
    },
    valgtSolposisjon() {
      return this.$store.state.solposisjon.utm ? true: false
    },
    vertikalvinkel() {
      if (this.solplassering) {
        return this.solplassering.altitude * 57.2957795
      }
    },
    solAzimuth() {
      if (this.solplassering) {
        return this.solplassering.azimuth * 57.2957795
      }
    }
  },
  watch: {
    time: function (val) {
      // TODO: Fiks problem med at datoen hopper når man når klokka 00:00
      if ((this.time !== 0 && this.minutter!=0) || (this.time !== 24 && this.minutter !== 0)) {
          this.dato.setHours(val)
      }
    },
    minutter: function (val) {
      this.dato.setMinutes(val)
      this.solplassering = this.kartView.hentSolPosisjon(this.solposisjon.wgs, this.dato)
      console.log(this.solplassering, this.dato, "Helvet");
      this.kartView.oppdaterLinje(this.solplassering)
    },
  },
  filters: {
    twoDigits (value) {
      if (value.toString().length <= 1) {
        return '0' + value.toString()
      }
      return value.toString()
    }
  },
  methods: {
    ...mapMutations(['SET_SOLPOSISJON', 'SET_GRADER']),

    velgPkt() {
      this.kartView.kartlag[3].removeAll()
      this.kartView.graphics.removeAll()
      this.valgtpkt = false
      // this.SET_SOLPOSISJON({wgs: null, utm: null})
      this.kartView.cursor = 'crosshair'
      this.kartView.solposPromise()
      .then(() => {
        this.solinfo = this.kartView.hentSolInfo(this.solposisjon.wgs, this.dato)
        this.kartView.cursor = 'default'
        this.valgtpkt = true
      }).catch(error => {console.log(error, 'Feilmeding');})
    },

    animerSol() {
      let vm = this
      let center = vm.kartView.center
      // console.log(this);
      vm.tid = vm.soloppgang
      this.solanimering = setInterval(function () {
        if (vm.tid < vm.solnedgang) {
            // console.log(vm.solnedgang, vm.tid, vm.dato)
            vm.solplassering = vm.kartView.hentSolPosisjon(vm.solposisjon.wgs, vm.dato)
            vm.kartView.oppdaterLinje(vm.solplassering)
            vm.tid += 2
        } else {
          clearInterval(interval)
          vm.tid = vm.soloppgang
        }
      }, 100)
    },

    stoppAnimerSol() {
      clearInterval(this.solanimering)
      this.solanimering = null
    },

    settDato() {
      if (this.valgtdato) {
        this.solinfo = null
        this.dato.setFullYear(this.valgtDatoArr[0])
        this.dato.setMonth(this.valgtDatoArr[1]-1)
        this.dato.setDate(this.valgtDatoArr[2])

        this.solinfo = this.kartView.hentSolInfo(this.solposisjon.wgs, this.dato)
      }
    },

    toggleOverflate() {
      if (this.kartView.overflateLag[0].visible) {
        this.overflateAktiv = true
        this.kartView.overflateLag[0].visible = false
        this.kartView.overflateLag[1].visible = true
      } else {
        this.kartView.overflateLag[0].visible = true
        this.kartView.overflateLag[1].visible = false
        this.overflateAktiv = false
      }
    }
  },
  mounted() {
    this.valgtpkt = false
    this.dato = new Date()
    this.valgtdato = this.dato.getFullYear() + '-' + (this.dato.getMonth()+1) + '-' + this.dato.getDate()
  },

  destroyed() {
    this.SET_SOLPOSISJON({wgs: null, utm: null})
    this.valgtpkt = false
    this.kartView.kartlag[3].removeAll()
  }
}
</script>

<style lang="css">
a{
  text-decoration: none;
  color: inherit !important;
}
#sol {
  /* position: absolute;
  top: 50px; */
  margin: 2px 0px;
  border: 2px solid #a6aaa4 ;
  border-radius: 4px;
  background-color: #c6cfc1;
  transition: all .3s;
  overflow: hidden;
}

@media (max-width: 495px) {
  #stedsinfo{
    top:45px;
    padding:0;
    margin:0px 3px;
    width: calc(100vw - 6px)
  }
}
</style>
