  methods: {
    urlSok () {
      var queryString = window.location.search; // henter querystring
      queryString = queryString.substring(1); // Fjerner ? fra starten

      var sok = {} // Oppretter et objekt for å lagre sok-parametre
      var sokliste = queryString.split('&') // deler opp soksparametrene i elementer

      sokliste.forEach(function (obj) { // itererer over soks-elementer
        var soklisteObj = obj.split('=') // deler opp elementene i type og verdi
        sok[soklisteObj [0]]=soklisteObj [1] // legger enkeltparametrene i søksobjekt
      })
      if (sok.tilt && sok.heading && sok.x && sok.y && sok.z) {
        var camera = {
          "position":{
            "spatialReference":{
              "latestWkid":25833,
              "wkid":25833
            },
            "x":sok.x,
            "y":sok.y,
            "z":sok.z
          },
          "heading":sok.heading,
          "tilt":sok.tilt
        }

        return camera
        // console.log("Krav oppfyllt", sok);
        // var grafikk = lagEnkeltPkt(sok.x, sok.y, markerSymbol)
        // console.log(grafikk);
        // view.graphics.add(grafikk)
        // view.camera = camera
      // }
    } else {
      return null
    }
    },
    delKart (camera) {
      var delKartInfo = {}
      delKartInfo.tilt = Math.round(camera.tilt)
      delKartInfo.heading = Math.round(camera.heading)
      delKartInfo.x = camera.position.x
      delKartInfo.y = camera.position.y
      delKartInfo.z = camera.position.z.toFixed(2)

      var queryString = ""
      for (var key in delKartInfo) {
          // check if the property/key is defined in the object itself, not in parent
          if (delKartInfo.hasOwnProperty(key)) {
            queryString =  queryString + key + "=" + delKartInfo[key] + "&"
          }
      }
      //  lagerUrl som skal brukes til deling
      var url = window.location.href.split("?")[0] + "?" + queryString
      // console.log(url);

      // prompt("Kopier og del kartlenke", url)
      return url
    }
  }
