<template>
  <v-layout
    row
    justify-center
    >
    <v-dialog
      attach="map"
      hide-overlay
      v-model="strava"
      scrollable
      max-width="450px"
      v-touch="{
        left: () => SET_STRAVA(false),
        right: () => SET_STRAVA(false)
        }"
      >
      <v-card color="ramme farge">
        <v-card-title primary-title class="pa-0 ma-0 ">
          <v-container fluid class="pa-0 ma-0" fill-height>
            <v-layout column>
              <span class="headline py-2">Strava-data</span>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs4 v-for="item in faneItems">
                  <v-card tile slot="activator" class="pa-0 ma-0 tittel-el" height="50px" flat ripple :class="[aktivFane === item.navn ? 'aktiv' : 'inaktiv']">
                    <v-layout @click="aktivFane = item.navn"  align-center justify-center fill-height class="knapp">
                        <v-icon large>{{ item.icon}}</v-icon>
                      <!-- <span hidden-xs-and-up>{{ item.navn }}</span > -->
                    </v-layout>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-layout>
          </v-container>
        </v-card-title>
        <v-card-text class="my-2">
          <strava-innstillinger v-if="aktivFane === 'Innstillinger'"></strava-innstillinger>
          <strava-data v-if="aktivFane === 'Spor'"></strava-data>
        </v-card-text>
      </v-card>

    </v-dialog>
  </v-layout>
</template>

<script>
import {mapState, mapGetters, mapMutations} from 'vuex'
import StravaInnstillinger from './StravaInnstillinger'
import StravaData from './StravaData'
import Leafmap from '../Leafmap'


export default {
  data() {
    return {
      kartsynlig: false,
      aktivFane: this.fane,
      faneItems: [
        {icon: 'settings',navn:'Innstillinger'},
        {icon: 'directions_run',navn:'Spor'},
        {icon: 'info',navn:'Informasjon'},
      ]
    }
  },
  props: {
    fane: {
      default: 'Innstillinger',
      type: String
    }
  },
  components: {StravaInnstillinger, StravaData, Leafmap},
  computed: {
    ...mapState(['stravaSynlig']),
    strava: {
      set(value) {
        this.$store.commit('SET_STRAVA', value)
      },
      get() {
        return this.stravaSynlig
      }
    },
  },
  methods: {
    ...mapMutations(['SET_STRAVA']),
    // source: http://doublespringlabs.blogspot.com.br/2012/11/decoding-polylines-from-google-maps.html

  }
}
</script>

<style lang="css" scoped>
.tittel-el{
  transition: all .3s
}

.tittel-el:hover{
  background-color: #a8b0a2 !important;
}

.inaktiv{
  background-color: #e1e9dc !important;
  color: rgb(94, 94, 94) !important;
  font-style: italic;
}

.aktiv{
  background-color: #c6cfc1 !important;
  color: inherit !important;
  font-style: normal;
  /* border-bottom: none !important; */
}

.knapp{
  cursor: pointer;
}


.ramme {
  border: 2px solid rgb(166, 170, 164) !important;
  border-radius: 4px !important;
}

.farge {
  background-color: #c6cfc1 !important;
}
</style>
