<template lang="html">
  <div id="leafkart" ref="leafkart" class="" style="height:200px; width: 100%">
  </div>

</template>

<script>
// import * as lmap from 'leaflet'
import decode from '@/assets/js/decodePolyline'
import * as L from 'leaflet'
import { Polyline, circleMarker } from 'leaflet'
export default {
  data() {
    return {
    antallPunkter: 0
    }
  },
  props: ['linje', 'start', 'finish'],

  mounted() {

    // console.log("Linje", typeof(this.linje) );
    // console.log("Start", typeof(this.start) );
    // console.log("Slutt", typeof(this.finish) );

    let kart = L.map(this.$refs.leafkart, {
      zoomControl:false
    }).setView([60.14, 10.25], 11);
    let layer = L.tileLayer('https://opencache.statkart.no/gatekeeper/gk/gk.open_gmaps?layers=topo4&zoom={z}&x={x}&y={y}', {
                opacity: 0.5
            }).addTo(kart);

    if (this.finish) {
      let finish = new circleMarker(this.finish, {color: 'red',  radius: 6 })
      finish.addTo(kart)
    }

    if (this.start) {
      let start = new circleMarker(this.start, {color: 'green', radius: 6 })
      start.addTo(kart)
    }

    if (this.linje) {
      let linje = this.linje
      if (typeof(this.linje) === "string") {
        linje = decode(this.linje).map(el => {
          return [el.latitude, el.longitude]
        })
      }

      // console.log(linje);

      let polylinje = new Polyline(linje, {
        color: 'orange',
        dashArray: '8,6'
      })

      polylinje.addTo(kart)
      kart.fitBounds(polylinje.getBounds(),{
        padding: [20,20]
      })
    }

    // console.log("Kartet er lastet");


  }
}
</script>

