<template lang="html">
  <v-layout column wrap class="text-xs-left">
    <p>Her vil det komme mulighet for å legge inn vegkart-api strenger og visualisere NVDB-data</p>
    <v-textarea
      v-model="apiKode"
      label="Vegkart api-streng"
      name="name"
      outline
    ></v-textarea>

    <v-btn color="primary" @click="hentNVDBdata">Last inn data</v-btn>
    <p>{{ query }}</p>
    <p>Objekttype {{ nvdbApiUrl }}</p>
  </v-layout>

</template>

<script>
import axios from 'axios'

export default {
  data(){
    return {
      apiKode: '',
      resultat: null
    }
  },
  computed: {
    nvdbApiUrl() {
      return decodeURIComponent(this.apiKode).split('?')[0]
    },
    query() {
      if (this.apiKode) {
        let query = {}
        decodeURIComponent(this.apiKode).split('?')[1].split('&').map((el) => {
          query[el.split('=')[0]] = el.split('=')[1]
        })
        return query
      } else {
        return "Helvete"
      }
      }
    },
  methods: {
    hentNVDBdata() {
      let options = {
        params: this.query,
        headers: {
          'X-Client': 'Test',
        }
      }
      options.params['inkludergeometri'] = 'lokasjon'
      axios.get(this.nvdbApiUrl, options)
      .then((el) => {
        console.log(el);
      })
      .catch((error) => {
        console.log(error);
      })
    }
  }
}
</script>

