import axios from "axios";
import { loadModules } from "esri-loader";
import {
  stravaPoint,
  stravaPtRenderer
} from "@/components/Map/js/strava_settings.js";

let addPtGraphic = null;
let createInfopt = null;

loadModules([
  "esri/Graphic",
  "esri/geometry/Point",
  "esri/layers/FeatureLayer"
]).then(([Graphic, Point, FeatureLayer]) => {
  addPtGraphic = function(pos, attributes, ptsymbol, graphicslayer) {
    var grafikk = new Graphic({
      geometry: new Point({
        x: pos.x,
        y: pos.y,
        spatialReference: {
          wkid: 25833
        }
      }),
      symbol: ptsymbol,
      attributes: attributes
    });
    graphicslayer.add(grafikk);
    return grafikk;
  };

  createInfopt = function(graphics) {
    let lyr = new FeatureLayer({
      source: graphics, // autocast as an array of esri/Graphic
      // create an instance of esri/layers/support/Field for each field object
      fields: stravaPoint, // This is required when creating a layer from Graphics
      objectIdField: "ObjectID", // This must be defined when creating a layer from Graphics
      renderer: stravaPtRenderer, // set the visualization on the layer
      spatialReference: {
        wkid: 25833
      },
      geometryType: "point", // Must be set when creating a layer from Graphics
      popupTemplate: null,
      title: "Strava-info",
      visible: true,
      legendEnabled: false
    });
    return lyr;
  };
});

function isMobileDevice() {
  return (
    typeof window.orientation !== "undefined" ||
    navigator.userAgent.indexOf("IEMobile") !== -1
  );
}

function urlSok() {
  var queryString = window.location.search; // henter querystring
  queryString = queryString.substring(1); // Fjerner ? fra starten

  var sok = {}; // Oppretter et objekt for å lagre sok-parametre
  var sokliste = queryString.split("&"); // deler opp soksparametrene i elementer

  sokliste.forEach(function(obj) {
    // itererer over soks-elementer
    var soklisteObj = obj.split("="); // deler opp elementene i type og verdi
    sok[soklisteObj[0]] = soklisteObj[1]; // legger enkeltparametrene i søksobjekt
  });
  if (sok.tilt && sok.heading && sok.x && sok.y && sok.z) {
    var camera = {
      position: {
        spatialReference: {
          latestWkid: 25833,
          wkid: 25833
        },
        x: sok.x,
        y: sok.y,
        z: sok.z
      },
      heading: sok.heading,
      tilt: sok.tilt
    };
    return camera;
    // console.log("Krav oppfyllt", sok);
    // var grafikk = lagEnkeltPkt(sok.x, sok.y, markerSymbol)
    // console.log(grafikk);
    // view.graphics.add(grafikk)
    // view.camera = camera
    // }
  }
}

function range(size, startAt = 0) {
  //  https://stackoverflow.com/a/10050831
  return [...Array(size).keys()].map(i => i + startAt);
}

function indexOfClosest(nums, target) {
  let closest = Number.MAX_SAFE_INTEGER;
  let index = 0;

  nums.forEach((num, i) => {
    let dist = Math.abs(target - num);
    if (dist < closest) {
      index = i;
      closest = dist;
    }
  });
  return index;
}

function sunCalc(x, y) {
  var solurl = "https://www.suncalc.org/#";
  // var zoom = '11'
  // var tidspkt = '12:00'
  var slutturl = "";
  var d = new Date();
  var dato = d.getFullYear() + "." + (d.getMonth() + 1) + "." + d.getDate();
  // var x = x;
  // var y = y;
  // slutturl = [solurl, y + ',' + x + ',' + zoom, dato, tidspkt].join('/')
  // console.log('Slutturl', slutturl);
  slutturl =
    "https://www.suncalc.org/#/" + y + "," + x + ",13/" + dato + "/12:00/1/2";
  return slutturl;
}

function yrUrl(lon, lat) {
  var resultatUrl = "";
  if (lon && lat) {
    var url =
      "https://www.yr.no/api/v0/locations/search?language=nb&lat=" +
      lat +
      "&lon=" +
      lon +
      "&accuracy=10";
    return axios.get(url);
    // var url = "https://www.yr.no/soek/soek.aspx?sted=" + navn + "&land=NO"
  }
}

function stedSok(pkt) {
  var urlSted = "https://www.norgeskart.no/ws/elev.py?";
  var options = {
    params: {
      lat: pkt.y,
      lon: pkt.x,
      epsg: 25833
    },
    timeout: 5000
  };
  return axios.get(urlSted, options);
}

function delKart(camera) {
  var delKartInfo = {};
  delKartInfo.tilt = Math.round(camera.tilt);
  delKartInfo.heading = Math.round(camera.heading);
  delKartInfo.x = camera.position.x;
  delKartInfo.y = camera.position.y;
  delKartInfo.z = camera.position.z.toFixed(2);

  var queryString = "";
  for (var key in delKartInfo) {
    // check if the property/key is defined in the object itself, not in parent
    if (delKartInfo.hasOwnProperty(key)) {
      queryString = queryString + key + "=" + delKartInfo[key] + "&";
    }
  }
  //  lagerUrl som skal brukes til deling
  var url = window.location.href.split("?")[0] + "?" + queryString;
  console.log(url);

  prompt("Kopier og del kartlenke", url);
}

function transformerKoord(x, y, fra, til) {
  var url = "https://ws.geonorge.no/transApi?";
  var options = {
    params: {
      ost: x,
      nord: y,
      fra: fra,
      til: til
    }
  };
  return axios.get(url, options);
}

export {
  delKart,
  addPtGraphic,
  transformerKoord,
  urlSok,
  stedSok,
  sunCalc,
  yrUrl,
  createInfopt,
  indexOfClosest,
  isMobileDevice,
  range
};
