<template lang="html">
  <v-card id="sol" v-if="verktoy.tegneMeny">
    <v-card-text class="pa-3 ma-0">
      <v-layout row wrap justify-space-between align-center>
        <v-btn class="pa-0 ma-0" icon color="primary" @click="open=!open" v-if="!open"><v-icon>keyboard_arrow_up</v-icon></v-btn>
        <v-btn class="pa-0 ma-0" icon color="primary" @click="open=!open" v-if="open"><v-icon>keyboard_arrow_down</v-icon></v-btn>
        <h3 v-if="open">Tegnemeny</h3>
        <v-btn class="pa-0 ma-0" icon color="primary" @click="lukkTegn" v-if="open"><v-icon>close</v-icon></v-btn>
      </v-layout>
      <v-divider class="my-2"></v-divider>
      <transition name="fadeHeight">
        <v-layout column wrap align-start justify-center class="pa-0 ma-0" v-if="open">
          <v-divider class="my-2" v-if="open"></v-divider>
          <article class="text-xs-left">
            <button class="tegneknapp" type="button" name="point" @click="tegnObjekt('point')" :class="{aktiv: erAktiv('point')}" ><v-icon>place</v-icon></button>
            <button class="tegneknapp" type="button" name="polyline" @click="tegnObjekt('polyline')" :class="{aktiv: erAktiv('polyline')}" ><v-icon>timeline</v-icon></button>
            <button class="tegneknapp" type="button" name="polygon" @click="tegnObjekt('polygon')" :class="{aktiv: erAktiv('polygon')}"><v-icon>bookmark_border</v-icon></button>
            <button class="tegneknapp" type="button" name="rectangle" @click="tegnObjekt('rectangle')" :class="{aktiv: erAktiv('rectangle')}"><v-icon>check_box_outline_blank</v-icon></button>
            <button class="tegneknapp" type="button" name="circle" @click="tegnObjekt('circle')" :class="{aktiv: erAktiv('circle')}"><v-icon>panorama_fisheye</v-icon></button>
          </article>
          <article id="drawhelpers" class="" v-if="redigeringAktiv">
            <button class="tegneknapp" type="button" name="undo" @click="sketch.undo()"><v-icon>undo</v-icon></button>
            <button class="tegneknapp" type="button" name="redo" @click="sketch.redo()"><v-icon>redo</v-icon></button>
            <button class="tegneknapp" id="freehandButton" name="freehand" type="button" title="Freehand" @click="freeHand" :class="{aktiv: aktivFreehand}" v-if="['polyline', 'polygon'].includes(aktivRedKnapp)"><v-icon>gesture</v-icon></button>
            <button class="tegneknapp" type="button" name="button" @click="" v-if="aktivRedKnapp"><v-icon>delete_forever</v-icon></button>
            <button class="tegneknapp" type="button" name="button" @click="resetKnapper" v-if="aktivRedKnapp"><v-icon>close</v-icon></button>
          </article>
          <article class="pt-3">

          </article>
          <article class="">
            <v-btn color="primary" @click="resetTegning">TØM</v-btn>
          </article>

          <article class="pt-3">
            <v-layout row wrap align-center v-if="!downloadLink">
              <h4>Lag gps-fil (gpx)</h4>
              <v-btn icon color="primary" @click="genererSpor"><v-icon>play_arrow</v-icon></v-btn>
            </v-layout>


            <v-layout row wrap align-center v-if="downloadLink">
              <v-flex xs10>
                <v-layout row wrap align-center>
                  <h4>Last ned spor</h4>
                  <button class="tegneknapp" type="button" name="button" @click=""><a id="download_link" download="test_gpx.gpx"  :href="downloadLink" ><v-icon>get_app</v-icon></a></button>
                </v-layout>
              </v-flex>
              <v-flex xs2>
                <v-btn icon color="transparent" @click="downloadLink = null"><v-icon>close</v-icon></v-btn>
              </v-flex>
            </v-layout>

            <v-btn color="primary" v-if="downloadLink"><a :href="gmapLink" target="#">Startpunkt</a></v-btn>
              <!-- <a :href="gmapLink" target="#">Link til startpunkt i gmaps</a> -->




            <!-- <v-layout row wrap v-if="genererer">
              <h4>Generer</h4>
              <v-progress-circular :value="20"></v-progress-circular>
            </v-layout> -->

          </article>
        </v-layout>
      </transition>

    </v-card-text>
  </v-card>
</template>

<script>
import {mapState, mapMutations, mapGetters} from 'vuex'
import Terraformer from 'terraformer-arcgis-parser'
import togpx from 'togpx'

export default {
  data() {
    return {
      tools: ['point', 'polyline', 'polygon'],
      redigeringAktiv: false,
      aktivRedKnapp: null,
      aktivFreehand: false,
      activeDraw: null,
      open: false,
      gmapLink: null
    }
  },
  computed: {
    ...mapState(['kartView', 'sketch', 'downloadLink', 'verktoy']),
    freehand() {
      if (this.sketch) {
        return this.sketch.activeComponent.activeAction
      }
    }
    // ...mapGetters(['oppdaterGrafikk']),
  },
  watch:{
    drawend: function() {
      if (this.sketch.state == "completed") {
        this.resetKnapper();
      }
    }
  },
  methods: {
    aktiverKnapp(verdi) {
      this.redigeringAktiv = !this.redigeringAktiv
      this.tools = [verdi]
      if (this.aktivRedKnapp === verdi) {
        this.aktivRedKnapp = null
      } else {
        this.aktivRedKnapp = verdi
      }
    },
    resetTegning() {
      this.sketch.layer.graphics.removeAll()
    },
    freeHand() {
      this.aktivFreehand = !this.aktivFreehand;
      this.sketch.activeComponent.activeAction.mode = this.sketch.activeComponent.activeAction.mode === "click" ? "hybrid" : "click";
    },
    erAktiv(verdi) {
      return this.aktivRedKnapp == verdi
    },
    resetKnapper() {
      this.aktivRedKnapp = null;
      this.aktivFreehand = false;
      this.redigeringAktiv = false;
      this.tools = ['point', 'polyline', 'polygon'];
      this.sketch.cancel();
      // this.view.graphics.removeAll();
    },
    tegnObjekt(verdi) {
      //  Fjerner eksisterende grafikk
      // this.$store.commit('oppdaterGrafikk', null)
      this.aktiverKnapp(verdi);
      this.sketch.create(verdi, {mode: "click"});
      console.log(this.sketch, 'Sketch');
    },
    genererSpor() {
      let spor = this.sketch.updateGraphics.items[0].geometry;
      this.gmapLink = this.kartView.genererGPX(spor)
    }
  },
  mounted() {
    // async function f() {
    //
    //     let promise = new Promise((resolve, reject) => {
    //       this.sketch.on("create", event => {
    //         // if(event.state === 'start') {
    //         //   kladdGrafikk.visible = true
    //         //
    //         // }
    //         if (event.state === 'complete') {
    //           alert('helvete')
    //
    //           let linje = this.kartView.konvertGeom(event.graphic.geometry)
    //           let geojsonLine = Terraformer.toGeoJSON(linje)
    //           console.log(geojsonLine);
    //           const gpx = togpx(geojsonLine)
    //           console.log(gpx)
    //           let data = new Blob([gpx], {type: 'text/plain'})
    //
    //           this.downloadLink = window.URL.createObjectURL(data)
    //
    //         }
    //       })
    //     })
    //
    //     let result = await Promise
    //
    //     alert("Eventlistener ok")
    // }


  }
}
</script>

<style lang="css" scoped>
#knappecontainer{
  position: absolute;
  bottom: 15px;
  right: 10px;
  display: flex;
  flex-flow: column nowrap;
  z-index: 4
}

.knapperad{
  margin: 2px;
  display: flex;
}

.tegneknapp {
  color: rgb(238, 142, 0);
  margin: 0 2px;
  height: 30px;
  width: 30px;
  border: 2px solid #ecb140;
  border-radius: 4px;
  background-color: rgb(255, 233, 212);
  transition: background-color 0.1s ease-in;
  cursor: pointer;
}

button:hover , .aktiv{
  background-color: #f9c667
}

button:active {
  background-color: #ffffff
}

button:focus{
  outline: 0
}

.right{
  justify-content: flex-end;
}

.fade-enter-active, .fade-leave-active {
transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

/* #freehandButton {
  position: absolute;
  bottom: 35px;
  color: black;
}

#resetBtn {
  position: absolute;
  bottom: 70px;
  color: black;
} */

@media (max-width: 480px) {
  #knappecontainer{
    bottom: 0px;
    right: 0px;
  }
}
</style>
