<template lang="html">
  <v-card id="stedsinfo">
    <v-card-text class="pa-1 ma-0">
      <v-layout column wrap align-start justify-start>
        <article class="text-xs-left pa-2" v-if="!stedsinfo.data.navn1 && !stedsinfo.timeout && !stedsdata.soker">
          <div class="title accent--text " >Klikk i kartet for å slå opp stedsinfo</div>
        </article>        
          <v-card color="transparent" width="100%" v-if="stedsinfo.data.navn1 && !stedsinfo.timeout" flat> 
            <v-toolbar flat color="primary" dark>
              <v-layout row wrap justify-space-between align-center>
                    <v-layout column wrap align-start class="text-truncate" >
                      <span class="headline white--text stedsnavn text-truncate">{{ navn }}</span>
                      <b> {{ hoyde }}</b>                                         
                    </v-layout>
                    <v-layout row wrap justify-end align-center my-0>
                      <v-btn color="transparent darken-1" icon><v-icon>zoom_in</v-icon></v-btn>
                      <div>
                        <v-btn class="pa-0 ma-0" icon color="primary" @click="open=!open" v-if="open"><v-icon>keyboard_arrow_up</v-icon></v-btn>
                        <v-btn class="pa-0 ma-0" icon color="primary" @click="open=!open" v-if="!open"><v-icon>keyboard_arrow_down</v-icon></v-btn>
                      </div>                      
                  </v-layout>
                  </v-layout>
            </v-toolbar>
            <article class="innhold" v-show="open">
            <v-tabs @change="test">
              <v-layout row wrap align-center>
                <div class="pr-4" >
                  <v-tab>
                    <v-icon>language</v-icon>
                  </v-tab>
                  <v-tab>
                    <v-icon>filter_drama</v-icon>
                  </v-tab>
                  <v-tab>
                    <v-icon>beenhere</v-icon>
                  </v-tab>
                </div>
                <div class="">
                  <h2 class="accent--text">{{ aktivTab }}</h2>
                </div>
              </v-layout>


              <v-tab-item>
                <v-card flat max-height="200px">
                  <v-card-text>
                    <v-layout column align-start>
                      <p>
                        <h2> {{ navn }}</h2>
                      </p>
                      <p>
                        <a :href="stedsFaktaUrl" target="#">Se fakta om stedsnavnet</a>
                      </p>

                    </v-layout>
                  </v-card-text>
                </v-card>
              </v-tab-item>
              <v-tab-item>
                <v-card flat max-height="200px" >
                  <v-card-text>
                    <v-layout column align-start>
                      <p>
                        <a :href="vaerVarselUrl" target="#">Se værvarsel</a>
                      </p>
                      <p>
                        <a :href="vaerVarsel24timerUrl" target="#">Se historiske værdata </a>
                      </p>
                      <p>
                        <a :href="vaerVarselRadarUrl" target="#">Se værradar</a>
                      </p>
                    </v-layout>
                  </v-card-text>
                </v-card>
              </v-tab-item>
              <v-tab-item>
                <v-card flat max-height="200px">
                  <v-card-text>
                      <div class="pa-1 subheading" >
                        <v-layout row wrap align-start>
                          <span @click="visKoord = !visKoord">Koordinat</span>
                          <br>
                          <transition name="fadeHeight" mode="out-in">
                            <div class="" v-if="visKoord">
                              <v-btn icon class="pa-1 ma-0"><v-icon color="grey darken-2" v-clipboard:copy="stedsinfo.data.x + '/' + stedsinfo.data.y ">link</v-icon></v-btn><span class="">{{ stedsinfo.data.x }} / {{stedsinfo.data.y}}</span>
                            </div>
                          </transition>
                        </v-layout>
                      </div>
                  </v-card-text>
                </v-card>
              </v-tab-item>
            </v-tabs>
           </article>
          </v-card>
        
        <!-- <article class="text-xs-left" v-if="stedsinfo.data.navn1 && !stedsinfo.timeout">
          <div class="pa-1">
            <span class="headline white--text">{{ navn }}</span>
          </div>
          <div class="pa-1 subheading">Høyde over havet: <b> {{ hoyde }}</b></div>
          <v-divider></v-divider>
          <div class="pa-1  subheading hidden-sm-and-down" v-if="stedsinfo.suncalc"><a :href="stedsinfo.suncalc" target="#">Se solgang</a></div>
          <div class="pa-1 subheading"v-if="stedsinfo.yrdata"><a :href="stedsinfo.yrdata" target="#">Se på været</a></div>

        </article> -->
        <article class="pa-1" v-if="stedsdata.soker">
        <!-- <article class="pa-1"> -->
          <v-layout row wrap justify-center align-center>
            <!-- <span class="title px-3">Søker</span> -->
            <v-progress-linear class="ma-3" style="width: 200px" color="accent" indeterminate></v-progress-linear>
            <!-- <v-progress-circular
            :size="30"
            color="primary"
            indeterminate
          ></v-progress-circular> -->
          </v-layout>
        </article>
        <article class="text-xs-left pa-1" v-if="stedsinfo.timeout">
          <v-layout row wrap justify-start align-center>
              <div class="pr-1 py-2 subheading"><v-icon color="accent" class="pr-2">error</v-icon>Stedssøket tok for lang tid. Prøv igjen</div>
          </v-layout>
        </article>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      visKoord: false,
      aktivTab: null,
      tabNavn: ["Generelt", "Vær", "Stedsinfo"],
      open: true
    };
  },
  props: ["stedsinfo"],
  computed: {
    ...mapState(["stedsdata"]),
    navn() {
      return (
        this.stedsinfo.data.navn1 +
        (this.stedsinfo.data.navn2 ? " / " + this.stedsinfo.data.navn2 : "")
      );
    },
    hoyde() {
      if (this.stedsinfo) {
        return this.stedsinfo.data.elev
          ? this.stedsinfo.data.elev + " moh"
          : "--";
      }
    },
    vaerVarselUrl() {
      return (
        "https://www.yr.no/nb/v%C3%A6rvarsel/daglig-tabell/" +
        this.stedsdata.yrData
      );
    },
    vaerVarsel30dagerUrl() {
      return (
        "https://www.yr.no/nb/historikk/tabell/" +
        this.stedsdata.yrData +
        "?q=siste-30-dager"
      );
    },
    vaerVarsel24timerUrl() {
      return (
        "https://www.yr.no/nb/historikk/tabell/" +
        this.stedsdata.yrData +
        "?q=siste-24-timer"
      );
    },
    vaerVarselRadarUrl() {
      return "https://www.yr.no/nb/kart/radar/" + this.stedsdata.yrData;
    },
    stedsFaktaUrl() {
      return (
        "https://stadnamn.kartverket.no/fakta/" +
        this.stedsdata.data.stedsnummer
      );
    }
  },
  methods: {
    test(a) {
      console.log(a);
      this.aktivTab = this.tabNavn[a];
    }
  }
};
</script>

<style lang="css">
.innhold {
  width: 100%;
}

.stedsnavn {
  max-width: 290px;
}

a {
  text-decoration: none;
  color: inherit !important;
}
#stedsinfo {
  position: absolute;
  top: 50px;
  margin: 2px 11px;
  width: 441px;
  /* max-width: 100%; */
  border: 2px solid #a6aaa4;
  border-radius: 4px;
  background-color: #c6cfc1;
}

@media (max-width: 495px) {
  #stedsinfo {
    top: 45px;
    padding: 0;
    margin: 0px 3px;
    width: calc(100vw - 6px);
  }

  .stedsnavn {
    max-width: 220px;
  }
}
</style>
