<template lang="html">
  <v-layout column wrap class="text-xs-left">
    <span class="headline pb-3 text-xs-center">Innstillinger</span>
    <v-layout column pb-2>
      <v-layout row wrap justify-space-between>
        <v-flex>
          <p>Applikason autorisert for Strava?</p>
        </v-flex>
        <v-flex class="text-xs-right">
          <v-icon class="px-2" v-if="stravaAutorisert" color="success">check_box</v-icon><v-icon v-else>check_box_outline_blank</v-icon>
        </v-flex>
      </v-layout>
      <v-flex class="text-xs-center">
        <v-btn color="green lighten-4" :href="'https://www.strava.com/oauth/authorize?client_id=26643&response_type=code&redirect_uri=' + aktivUrl + '&approval_prompt=force&scope=activity:read_all'" v-if="!stravaAutorisert">Autoriser</v-btn>
      </v-flex>
    </v-layout>

    <v-layout row wrap align-center justify-space-between>
      <p>Deautoriser Strava-tilgang</p>
      <v-btn flat tile icon style="position:relative;top:-7px; left:5px" href="https://www.strava.com/settings/apps" target="#" @click="slettStravacode"><v-icon color="grey darken-1">delete</v-icon></v-btn>
    </v-layout>
    <v-layout row wrap justify-space-between>
      <v-flex>
        <p>Tilgangstype</p>
      </v-flex>
      <v-flex class="text-xs-right font-italic">
        Les privat Strava-data
      </v-flex>
    </v-layout>

  </v-layout>

</template>

<script>
import { localStore } from '@/storage'

export default {
  data() {
    return {
        stravaAutorisert: false,
        kartUrl: 'https://kart.skoiddlos.com/test',
        lokalUrl:'http://localhost:8081'
    }
  },
  computed: {
    aktivUrl() {
        if (location.hostname === "localhost" || location.hostname === "127.0.0.1") {
          return this.lokalUrl
        } else {
          console.log(location.hostname);
          return this.kartUrl
        }
      }
  },
  methods: {
    slettStravacode() {
      localStore.removeItem('auth_code')
      this.stravaAutorisert = false
    },
  },
  mounted() {
    this.stravaAutorisert = localStore.getItem('auth_code') ? true : false
  }
}
</script>

<style lang="css">
</style>
