import Terraformer from 'terraformer-arcgis-parser'
import togpx from 'togpx'
// import Todo from '@/components/administrering/Todos'

export default {
  name: "Map",
  data() {
    return {
    fullskjermVM: null,
    lokaliserVM: null,
    kartLastet: false,
    camera: null,
    mapInteracting: false,
    cameraList: [],
    cameraIndex: null,
    posisjon: null,
    mobileDevice: false,
    downloadLink: null,
    regObslag: null,

    }
  },
  components: {VerktoyUr,VerktoyBr, Stedsok, Modal, Innstillinger, Sidemeny, Strava, Loader, MaaleDrit, DataModal, StedsInfo, Solsimulering, Tegnemeny, Tegnforklaring},
  computed: {
    ...mapState(['kartView', 'verktoy', 'stedsdata', 'aktivKlikkEvent', 'grader', 'solposisjon']),
    ...mapGetters(['maale']),
    verdi() {
      return this.maale ? 'Helvete': 'Faen'
    },
    interaksjon() {
