    },
  },
  watch: {
    grader: function () {
      this.kartView.oppdaterLinje()
    }
  },
  mixins: [ mapTools ],
  methods: {
    ...mapMutations([
                     'SET_VIEW', 'SET_KARTLAG', 'SET_BASELAG', 'TOGGLE_TOOL',
                     'TOGGLE_MENY', 'SET_KARTLASTET', 'SET_AKTIVELAG', 'SET_STEDSNAVN',
                     'SET_STEDSDATA', 'SET_INPUTSYNLIG', 'SET_KLIKKEVENT', 'SET_DIALOG', 'SET_SOLPOSISJON',
