    }
    // console.log('map: mounted');
    const vm = this
    this.camera = this.urlSok()
    loadModules([
      'esri/Map',
      'esri/geometry/projection',
      'esri/layers/TileLayer',
      'esri/layers/ElevationLayer',
      'esri/layers/MapImageLayer',
      'esri/layers/WMSLayer',
      'esri/layers/GraphicsLayer',
      'esri/layers/FeatureLayer',
      'esri/Basemap',
      'esri/geometry/SpatialReference',
      'esri/views/SceneView',
      'esri/geometry/Extent',
      'esri/widgets/Fullscreen/FullscreenViewModel',
      'esri/widgets/Locate/LocateViewModel',
      'esri/Graphic',
      'esri/geometry/Point',
      'esri/geometry/Polyline',
      'esri/geometry/Polygon',
      'esri/Viewpoint',
      'esri/config',
      'esri/request',
      'esri/core/watchUtils',
      'esri/widgets/DirectLineMeasurement3D',
      'esri/geometry/geometryEngine',
      'esri/views/2d/draw/Draw',
      'esri/widgets/Sketch',
      'esri/widgets/Sketch/SketchViewModel',
      'dojo/on',
      "esri/PopupTemplate",
      'esri/renderers/support/jsonUtils',
      "esri/widgets/Legend"
    ],
    // {
    //   url: 'https://js.arcgis.com/4.12'
    // }
  ).
    then(([
      Map, projection, TileLayer, ElevationLayer, MapImageLayer, WMSLayer, GraphicsLayer, FeatureLayer, Basemap, SpatialReference, SceneView, Extent, FullscreenVM, LocateVM,
