    ]) => {
      esriConfig.request.corsEnabledServers.push("www.norgeskart.no");
      esriConfig.request.corsEnabledServers.push("ws.geonorge.no");
      esriConfig.request.corsEnabledServers.push("wms.geonorge.no");
      esriConfig.request.corsEnabledServers.push("api.nve.no");
      esriConfig.request.corsEnabledServers.push("geo.ngu.no");
      esriConfig.request.corsEnabledServers.push("wms3.nve.no");
      esriConfig.request.corsEnabledServers.push("gis3.nve.no");

      const euref89 = new SpatialReference(25833)


      // Create elevation layers
      let bakke = new ElevationLayer({
        url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheTerreng/ImageServer',
        // url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheOverflate/ImageServer',
        visible: true
      });

      let overflate = new ElevationLayer({
        // url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheTerreng/ImageServer',
        url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheOverflate/ImageServer',
