          visible: true
        });

      let graatone = new TileLayer({
          url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheGraatone/MapServer',
          id: 'Gråtone',
          visible: false
        });

      let landskap = new TileLayer({
            url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheLandskap/MapServer',
            id: 'Landskap',
            visible: false
          });

      const kyst = new TileLayer({
        url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheKyst/MapServer',
        id: "Kyst",
        visible: false
      })

      const hybrid = new TileLayer({
        url: 'https://services.geodataonline.no/arcgis/rest/services/Geocache_UTM33_EUREF89/GeocacheHybrid/MapServer',
        id: "Hybrid",
        visible: false
      })

      let skredData = new FeatureLayer({
        url: 'https://gis3.nve.no/map/rest/services/SkredHendelser/MapServer/1',
        id: 'Skredhendelser',
        visible: false,
        renderer: rendererJsonUtils.fromJSON(JSON.parse(this.skredRenderer)),
        popupTemplate: PopupTemplate.fromJSON(JSON.parse(this.skredPopup)),
        definitionExpression: "skredType = 133 OR skredType = 130 OR skredType = 140 OR skredType = 131 OR skredType = 132 OR skredType = 134 OR skredType = 135 OR skredType = 136 OR skredType = 137 OR skredType = 138 OR skredType = 139 OR skredType = 151"
      })

      console.log(skredData, "Skreddata");


      let bratthet = new MapImageLayer({
        url: 'https://gis3.nve.no/map/rest/services/Bratthet/MapServer',
        id: 'Helling',
        sublayers: [
            {
              id: 0,
              visible: true,
              opacity: .5,
              legendEnabled: true,
            }
          ],
          visible: false
        });

        // let skredHendelser = new MapImageLayer({
        //   url: 'https://gis3.nve.no/map/rest/services/SkredHendelser/MapServer',
        //   sublayers: [
        //       {
        //         id: 'Skredhendelser',
        //         visible: false,
        //         legendEnabled: true,
        //       }
        //     ]
        //   });

        console.log(projection.load(), "Helvete");

        let eiendom = MapImageLayer({
          url: "http://services2.geodataonline.no/arcgis/rest/services/temp/GeomapEiendom/MapServer",
          id: "Matrikkel",
          visible: false
        })

        let kladdGrafikk = new GraphicsLayer({
          id: 'Kladd',
          visible: false,
        })

        let sporGrafikk = new GraphicsLayer({
          id: 'Spor',
          visible: false,
        })

        let solGrafikk = new GraphicsLayer({
          id: 'Spor',
          visible: false
        })

      const baseLayers = [bilder, graatone, landskap, kyst]
      const mapLayers = [bratthet, eiendom, sporGrafikk, kladdGrafikk, solGrafikk, hybrid, skredData]
      const surface = [bakke, overflate]

      let bakgrunnsKart = new Basemap({
        baseLayers: baseLayers,
        title: 'Bakgrunnskart',
        id: 'bakgrunnskart'
      });

      let extent = new Extent ({
        xmax:624096.1043825686,
        xmin:380255.6167015933,
        ymax:7761884.8668109365,
        ymin:7415089.506553549,
        spatialReference: {
          wkid: 25833
        }
      })

      let map = new Map({
        basemap: bakgrunnsKart,
        layers: mapLayers,
        ground: {
          layers: surface
        }
      });

      const view = new SceneView({
        container: "map",
        map: map,
        extent: extent,
        camera: this.camera ? this.camera : this.defaultCamera
      });

      var legend = new Legend({
        view: view,
        container: "#legend"
      });

      const punkt = {
        type: "simple-marker",
        style: "diamond",
        outline: { width: 1.25, color: [255, 0, 0, 1] },
        angle: 0,
        size: 12,
        color: [255, 170, 0, 0.25]
       };


      const linje = { type: "simple-line", width: 3, color: [230, 0, 0, 1] };

      const flate = {
        type: "simple-fill",
        style: "solid",
        color: [
          255,0,0,0.6
        ],
        outline: {
          color: [255,0,0,1],
          width: 4
        }
      }

      const sketchVM = new SketchViewModel({
        pointSymbol: punkt,
        polylineSymbol: linje,
        polygonSymbol: flate,
        view: view,
        layer: kladdGrafikk,
      })


      // TODO: Bygg om denne til en SketchViewModel
      // ----------   Sketch prototype  ---------
      // const sketch = new Sketch({
      //   layer: kladdGrafikk,
      //   view: view,
      //   container: "tegnebrett"
      // })

      // sketch.viewModel.polygonSymbol = flate
      // sketch.viewModel.pointSymbol = punkt
      // sketch.viewModel.polylineSymbol = linje
      // sketch.viewModel.defaultUpdateOptions.toggleToolOnClick = true

      sketchVM.on("create", event => {
        if(event.state === 'start') {
          kladdGrafikk.visible = true
        }
        else if (event.state === 'complete') {

        }
      })


      // view.ui.add(sketch, "bottom-left")
      view.ui.remove(['zoom', 'attribution', 'navigation-toggle', 'compass'])
      view.kartlag = mapLayers
      view.overflateLag = surface
      console.log(view.kartlag, "Kartlag");

      let fullscreen = new FullscreenVM({
        element: this.$refs.map,
        view: view
      })

      let draw = new Draw({
        view: view
      });

      let preventPan = false

      view.tegnLinje = function () {
        let action = draw.create("polyline", {mode: "click"});
        // fires when a vertex is added
        action.on("vertex-add", function (evt) {
          drawPolyline(evt);
        });

        // fires when the pointer moves
        action.on("cursor-update", function (evt) {

          drawPolyline(evt);
        });

        // fires when the drawing is completed
        action.on("draw-complete", function (evt) {
          view.cursor = 'default'
          drawPolyline(evt);
        });

        // fires when a vertex is removed
        action.on("vertex-remove", function (evt) {
          drawPolyline(evt);
        });

        vm.SET_AKTIVDRAW(action)
      }

      view.tegnPolygon = function () {
        let action = draw.create("rectangle", {mode: "click"});
        // fires when a vertex is added
        action.on("vertex-add", function (evt) {
          drawPolygon(evt);
        });

        // fires when the pointer moves
        action.on("cursor-update", function (evt) {

          drawPolygon(evt);
        });

        // fires when the drawing is completed
        action.on("draw-complete", function (evt) {
          view.cursor = 'default'
          drawPolygon(evt);
        });

        // fires when a vertex is removed
        action.on("vertex-remove", function (evt) {
          drawPolygon(evt);
        });

        console.log(action);

        vm.SET_AKTIVDRAW(action)
      }



      let locate = new LocateVM({
        view: view,
        scale: 3000
      })

      function drawPoint(pos) {
        console.log(event, "Dette er event");
        let grafikk = new Graphic({
          geometry: new Point({
              x: pos.x,
              y: pos.y,
              spatialReference: {
                wkid: 25833
              }
            }),
          symbol: {
              type: "simple-marker",
              outline: {
                  width: 1.75,
                  color: [255, 0, 0, 1]
              }
          }
        })
        view.graphics.removeAll()
        view.graphics.add(grafikk)
      }

      function konverterPkt(el, srid) {
        let fra, til
        srid == "wgs84" ? til = SpatialReference.WGS84 : fra = SpatialReference.WGS84
        srid == "euref89" ? til = euref89 : fra = euref89
        let point = new Point({
          x: el[0],
          y: el[1],
          spatialReference: fra
        })

        let konvertertPkt = projection.project(point, til)
        console.log(konvertertPkt, 'Heløvetessdfgsdf sdfg ');
        return [konvertertPkt.x, konvertertPkt.y]
      }

      function drawPolyline(event) {
        console.log('Tegner polylinje');
        // let point = view.toMap(event.native.screenX, event.native.screenY)
        // console.log(point);
        var vertices = event.vertices;

        //remove existing graphic
        view.graphics.removeAll();

        var graphic = new Graphic({
        geometry: {
          type: "polyline", // autocasts as Polygon
          rings: vertices,
          spatialReference: view.spatialReference
        },
        symbol: {
          type: "simple-line",  // autocasts as new SimpleLineSymbol()
          color: "lightblue",
          width: "2px",
        }
      });
        view.graphics.add(graphic);

        // calculate the area of the polygon
        var length = geometryEngine.planarLength(polyline, "kilometers");
        // start displaying the area of the polygon
        labelAreas(polyline, length);
      }

      function drawPolygon(event) {
        // console.log('Tegner polygon');
        let pos2 = view.toMap({x:event.native.clientX, y:event.native.clientY})
        var vertices = event.vertices;
        let pos1 = {x: event.vertices[0][0], y: event.vertices[0][1]}

        //remove existing graphic
        view.graphics.removeAll();
        let paths = event.vertices

        if (event.vertices[0]) {
          paths = [
            [pos1.x, pos1.y],
            [pos1.x, pos2.y],
            [pos2.x, pos2.y],
            [pos2.x, pos1.y],
            [pos1.x, pos1.y]
          ]
        }

        var graphic = new Graphic({
        geometry: {
          type: "polygon", // autocasts as Polygon
          rings: paths,
          spatialReference: view.spatialReference
        },
        symbol: {
          type: "simple-fill", // autocasts as SimpleFillSymbol
          color: "rgba(252, 168, 0, 0.69)",
          style: "solid",
          outline: {  // autocasts as SimpleLineSymbol
            color: "rgb(255, 140, 0)",
            width: 3
          }
        }
      });
        view.graphics.add(graphic);
      }

      function getExtent() {

      }

      function velgOmraade() {
        preventPan = true
        let hentOmr = new Promise(
          function(resolve, reject) {
            const startKam = view.camera
            view.goTo({
              heading:0,
              scale: view.scale * 3,
              tilt: 20
            })
            let action = draw.create("rectangle", {mode: "click"});
            view.cursor = "crosshair"
            // fires when a vertex is added
            action.on("vertex-add", function (evt) {
              let pos = {
                x: evt.vertices[0][0],
                y: evt.vertices[0][1]
              }
            });

            // fires when the pointer moves
            action.on("cursor-update", function (evt) {
              drawPolygon(evt);
            });

            // fires when the drawing is completed
            action.on("draw-complete", function (evt) {
              view.goTo({target:startKam})
              if (evt.vertices.length > 0)  {
                let pos1 = {
                  x: evt.vertices[0][0],
                  y: evt.vertices[0][1]
                }
                let pos2 = {
                  x: evt.vertices[1][0],
                  y: evt.vertices[1][1]
                }
                view.cursor = 'default'
                // drawPolygon(evt);
                setTimeout(function() {
                  view.graphics.removeAll()
                }, 1000)
                view.cursor = "default"
                let resultat = {
                  centerpkt: {
                    x: (pos1.x + pos2.x) / 2,
                    y: (pos1.y + pos2.y) / 2
                  },
                  maxRadius: Math.round(Math.max(Math.abs(pos1.x - pos2.x), Math.abs(pos1.y - pos2.y))),
                  vertices: [pos1, pos2]
                }
                console.log(resultat);
                resolve(resultat)
                preventPan = false
              } else {
                let message = new Error('Objekt ble ikke tegnet riktig. Prøv igjen')
                reject(message)
              }
            });
          }
        )
        return hentOmr
      }

      function projectCoord(x,y,fra,til) {
        let pkt = new Point({
          x: x,
          y: y,
          spatialReference: {
            wkid: fra
          }
        })
        return projection.project(pkt, {wkid: til})
      }

      function eiendomsInfo(pkt) {
        var urlSted = 'https://ws.geonorge.no/adresser/v1/punktsok?'
        var options = {
          params: {
            lat: pkt.y,
            lon: pkt.x,
            radius: 30,
            treffPerSide: 10,
            side: 1
          },
          timeout: 5000
        }
        return axios.get(urlSted, options)
      }

      function eiendomsInfo2(pkt) {
        var urlSted = 'https://www.norgeskart.no/ws/wfs.teig.py?'
        var options = {
          params: {
            bbox: pkt.y + "," + pkt.x + "," + pkt.y + "," + pkt.x
          }


        }
        return axios.get(urlSted, options)
      }

      function queryOptionsNVE(){
        var options = {
          query: {
            where: 'skredtype in (130,131,132,133,134,135,136,137,138,139)',
            geometry:'{xmin: ' + view.extent.xmin + ', ymin: ' + view.extent.ymin + ', xmax: ' + view.extent.xmax + ', ymax: ' + view.extent.ymax + '}',
            geometryType: 'esriGeometryEnvelope',
            inSR:28533,
            spatialRel: 'esriSpatialRelIntersects',
            // outFields: 'skredType, skredNavn, stedsnavn,vaerObservasjonbeskrivelse, objektType, utlosningArsak, dokumentasjon',
            outFields: '*',
            returnGeometry:true,
            returnTrueCurves:false,
            outSR:25833,
            returnIdsOnly:false,
            returnCountOnly:false,
            returnZ:false,
            returnM:false,
            returnDistinctValues:false,
            returnExtentsOnly:false,
            f: 'geojson',
          },
          responseType: 'json'
        };
      return options
      }

        function hentRegObs(valgtomr) {

        //Slett eksisterende fefaturelayer hvis det eksisterer


        let pos = konverterPkt([valgtomr.centerpkt.x, valgtomr.centerpkt.y], "wgs84")
        console.log(pos, "Konvertert pkt");

        let url = "https://api.nve.no/hydrology/regobs/webapi_v3.2.0/search/withinradius/";
        let data = {
