      function datoDiff(dato){
        var d = new Date()
        var dagensDato = [d.getDate(), d.getMonth()+1, d.getFullYear()].join('/')
        var b = moment(dagensDato, 'DD/MM/YYYY');
        var a = moment(dato, 'YYYY-MM-DD');
        return b.diff(a, 'days');
        }

      function lagRegObsKart (data) {
        var regFields = [
          {
            name:'ObjectID',
            alias: 'ObjectID',
            type: "oid"
          },
          {
            name:'regid',
            alias:'RegID',
            type: "string"
          },
          {
            name:'type',
            alias:'Type observasjon',
            type: "string"
          },
          {
            name: 'regtid',
            alias: 'Observasjonstidspunkt',
            type: 'string'
          },
          {
            name: 'aar',
            alias: 'År',
            type: 'integer'
          },
          {
            name: 'tidsidenreg',
            alias: 'Tid siden registrering',
            type: 'integer'
          }
        ];

        var regRenderer = {
              type: "class-breaks", // autocasts as new ClassBreaksRenderer()
              field: "tidsidenreg",
              // normalizationField: "EDUCBASECY",
              defaultSymbol: {
                      type: "point-3d",
                      symbolLayers: [{
                        size: 18,
                        type: "icon",
                        outline: {
                            color: [255, 0, 0, 0.5],
                             size: 2
                        },
                        material: {
                            color: [126, 126, 126, 0.3]
                        }
                      }]
                  },
              defaultLabel: "Eldre enn en mnd",
              classBreakInfos: [{
                minValue: 0,
                maxValue: 3,
                symbol: {
                        type: "point-3d",
                        symbolLayers: [{
                          size: 18,
                          type: "icon",
                          outline: {
                              color: [255, 0, 0, 0.8],
                               size: 2
                          },
                          material: {
                              color: [112, 255, 0, 0.6]
                          }
                        }]
                    },
                label: "Under 3 dager siden"
              }, {
                minValue: 4,
                maxValue: 8,
                symbol: {
                        type: "point-3d",
                        symbolLayers: [{
                          size: 18,
                          type: "icon",
                          outline: {
                              color: [255, 0, 0, 0.8],
                               size: 2
                          },
                          material: {
                              color: [191, 237, 125, 0.6]
                          }
                        }]
                    },
                label: "Under en uke siden"
              }, {
                minValue: 9,
                maxValue: 30,
                symbol: {
                        type: "point-3d",
                        symbolLayers: [{
                          size: 18,
                          type: "icon",
                          outline: {
                              color: [255, 0, 0, 0.8],
                               size: 2
                          },
                          material: {
                              color: [219, 223, 0, 0.6]
                          }
                        }]
                    },
                label: "Under en mnd siden"
              }]
            };

        var regPopup = {
              title: "regObs-observasjon",
              content: "<p>Observasjonstidspunkt: {regtid}</br>Dager siden registrering: <b>{tidsidenreg}</b></br></br><a href='http://www.regobs.no/Registration/{regid}' target='#'>Gå til observasjon</a></p>"
            };

        // var lagObj = {
        //   regobs: regObsFeature
        // }
        // console.log(data, "Datatest");

        let grafikk = data.Results.map(function(feature, i) {
          // console.log(feature);
          var tid = {}
          var tidsStreng = feature.DtObsTime.split('T')
          tid.dato = tidsStreng[0]
          tid.klokke = tidsStreng[1].split('.')[0]
          tid.aar = parseInt(tid.dato.split("-")[0])
          var obstid = tid.dato + ', kl. ' + tid.klokke.slice(0,5)
          return {
            geometry: new Point({
              x: feature.UtmEast,
              y: feature.UtmNorth,
              spatialReference: {
                wkid: 25833
              }
            }),
            // select only the attributes you care about
            attributes: {
              ObjectID: i,
              regid: feature.RegId.toString(),
              type: feature.GeoHazardName,
              regtid: obstid,
              aar: tid.aar,
              tidsidenreg: datoDiff(tid.dato)
            }
          };
        });


          if (vm.regObslag) {
              vm.regObslag.queryFeatures()
                  .then(response => {
                      vm.regObslag.applyEdits({deleteFeatures:response.features})
                  })
              //Oppdaterer kilden til featurelaget hvis det allerede eksisterer
              vm.regObslag.applyEdits({addFeatures: grafikk})
              console.log("Oppdatert grafikk")
          } else {
              //Lager et nytt featurelag for regobs hvis det ikke eksisterer
              var regObsFeature = new FeatureLayer({
              id: "regObs-observasjoner",
              source: grafikk, // autocast as an array of esri/Graphic
              // create an instance of esri/layers/support/Field for each field object
              fields: regFields, // This is required when creating a layer from Graphics
              objectIdField: "ObjectID", // This must be defined when creating a layer from Graphics
              renderer: regRenderer, // set the visualization on the layer
              spatialReference: {
                wkid: 25833
              },
              geometryType: "point", // Must be set when creating a layer from Graphics
              popupTemplate: regPopup,
              title: "regObs-observasjoner",
                  visible: true,
              });
              console.log("Nytt regobslag")
              vm.regObslag = regObsFeature
              mapLayers.push(regObsFeature)
          }



       // console.log(regObsFeature, "Regobsdata")

        view.map.add(regObsFeature)
      }

      function hentSkredHendelser(valgtomr) {
        let url = 'https://gis3.nve.no/map/rest/services/SkredHendelser/MapServer/1/query?'
        let params = {
          // where: 'skredtype in (130,131,132,133,134,135,136,137,138,139)',
          where: '1=1',
          geometry: '{xmin: ' + valgtomr.vertices[0].x + ', ymin: ' + valgtomr.vertices[0].y + ', xmax: ' + valgtomr.vertices[1].x + ', ymax: ' + valgtomr.vertices[1].y + '}',
          geometryType: 'esriGeometryEnvelope',
          inSR:25833,
          spatialRel: 'esriSpatialRelIntersects',
          // outFields: 'skredType, skredNavn, stedsnavn,vaerObservasjonbeskrivelse, objektType, utlosningArsak, dokumentasjon',
          outFields: '*',
          returnGeometry:true,
          returnTrueCurves:false,
          outSR:25833,
          returnIdsOnly:false,
          returnCountOnly:false,
          returnZ:false,
          returnM:false,
          returnDistinctValues:false,
          returnExtentsOnly:false,
          f: 'geojson',
        }

        axios.post(url, params)
        .then(response => {
          console.log(response, "Response fra skredhendelser");
        })
      }

