      view.oppdaterLinje = function (solobj) {
        let polylineGraphic = null
        // let altAngle = 1.0211052908793197
        // let hoydeOverHorisont = Math.sin(hoydeang) * l

        function testLinje (l, pos, ang, hoydeang) {
          // let faen = ang - 90
          // let angle = faen * Math.PI / 180
          let hoydeOverHorisont = Math.sin(hoydeang) * l
          let angle = ang + (Math.PI/2)

          let x = pos[0] + Math.cos(angle) * l    // unchanged
          let y = pos[1] - Math.sin(angle) * l    // minus on the Sin
          return [x,y,hoydeOverHorisont]
        }

        function lagLinje (solobj) {
          let pos = vm.solposisjon.utm
          // let pos2 = testLinje(20000, pos, vm.grader)
          // console.log(solobj.azimuth, 'Heading', solobj.altitude);
          let pos2 = testLinje(20000, pos, solobj.azimuth, solobj.altitude)
          let rings = [
            pos,
            [pos2[0]+5,pos2[1]+5, pos2[2]],
            [pos2[0],pos2[1], 10],
            // [pos[0]+5, pos[1]+5, 0],
            pos
          ]
          polylineGraphic = new Graphic({
            geometry: new Polygon({
              rings: rings,
              hasZ: true,
              spatialReference: {
                wkid: 25833
              }
            }),
            symbol: {
              type: "simple-fill", // autocasts as new SimpleFillSymbol()
              color: "rgba(227, 209, 36, 0.59)",
              outline: { // autocasts as new SimpleLineSymbol()
                color: "rgb(254, 226, 60)",
                width: 3
              }
            }
          })

          var sunGraphic = new Graphic({
            geometry: new Point({
              x: pos2[0],
              y: pos2[1],
              z: pos2[2],
              hasZ: true,
              spatialReference: {
                wkid: 25833
              }
            }),
            symbol: {
              type: "point-3d",  // autocasts as new PointSymbol3D()
              symbolLayers: [{
                type: "object",  // autocasts as new ObjectSymbol3DLayer()
                width: 1000,  // diameter of the object from east to west in meters
                height: 1000,  // height of the object in meters
                depth: 1000,  // diameter of the object from north to south in meters
                resource: { primitive: "sphere" },
                material: { color: "yellow" }
              }]
            }
          })

          solGrafikk.removeAll()
          solGrafikk.graphics.addMany([polylineGraphic, sunGraphic])
        }

        if (vm.solposisjon) {
          lagLinje(solobj)
        } else {
          // vm.solposisjon
        }
        return polylineGraphic
      }


      this.fullskjermVM = fullscreen
      this.lokaliserVM = locate
      // console.log(this.kartLastet, 'Før endring');
      bilder.watch("loaded", ( newValue, oldValue ) => {
        if (newValue) {
          this.kartLastet = true
        }
      })
      view.watch("camera.heading", ( newValue, oldValue ) => {
        if (newValue) {
          this.$store.commit('SET_MAPINFO', {attribute: 'heading', value: newValue})
        }
      })
      view.watch("stationary", ( newValue, oldValue ) => {
        if (newValue) {
          vm.mapInteracting = newValue
          // console.log(vm.mapInteracting);
        } else {
          view.updateCameralist()
          vm.cameraIndex = vm.cameraList.length - 1
        }
      })
      // console.log(this.lokaliserVM, "Helvete");
      view.when(() => {
        // function testLinje (l, pos, ang) {
        //   let solgrafikk = null
        //   let faen = ang - 90
        //   let angle = faen * Math.PI / 180
        //   let x = pos[0] + Math.cos(angle) * l    // unchanged
        //   let y = pos[1] - Math.sin(angle) * l    // minus on the Sin
        // 	// return [x,y]
        //   return [x,y,400]
        // }
        console.log(view, 'Kartet');
        console.log(legend, "Legend");
        view.on('click', event => {
          try {
            // console.log(event, "Event")

            let konvertertPkt = projection.project(event.mapPoint, {wkid: 4326})
            // console.log("Konvertert punkt", konvertertPkt);
            let grader = 0
            if (event.mapPoint.x) {
              let pos = [event.mapPoint.x, event.mapPoint.y, 40]
              vm.SET_SOLPOSISJON({utm:pos})
            }
            // console.log(event);
          } catch (e) {
            // console.log(e);
          } finally {

          }

          view.hitTest(event)
          .then(response => {
            // console.log(response);
            if (response.results.length>0) {
              //let lasteNed = confirm("Laste ned spor til gpx-fil?")

              //if (lasteNed) {
              //  let geom = response.results[0].graphic.geometry
              //  view.genererGPX(geom)
              //}
            }


          })

        })

        view.on("key-down", evt => {
          // console.log(evt, "helvet");
          if (evt.key === "Shift") {
            // console.log("Shifty");
            sketchVM.create("rectangle")


          }
        })
        // console.log(this, "vm-instance");
        // console.log(directLMeasurement, 'måling');
        this.SET_VIEW(view);
        this.SET_BASELAG(baseLayers);
        this.SET_KARTLAG(mapLayers);
        // this.SET_KARTLAG(view.map.layers.items);
        this.SET_SKETCH(sketchVM)

        view.on("click", (event) => {
          if (this.verktoy.punktZoom) {
            // console.log('Helvet');
            let pos = {
              x: event.mapPoint.x,
              y: event.mapPoint.y
            }
            view.zoomTilPunkt(pos)
          }
        })

        view.on("drag", (event) => {
          if (preventPan) {
            event.stopPropagation()
          }
        })

      })
    })
    .catch( err => {
      console.error(err)
    })
  }
}
</script>

